 import {
     utcHours,
     utcMins
 } from "@/store/variable";

 export const globalMixin = {
     methods: {
         goBack() {
             window.history.length > 1 ?
                 this.$router.go(-1) :
                 this.$router.push("/");
         },

         getDateTime(givenDate) {
             let dateTime = new Date(givenDate);
             dateTime.setHours(dateTime.getHours() + utcHours);
             dateTime.setMinutes(dateTime.getMinutes() + utcMins);
             let month = dateTime.getMonth() + 1;
             let date =
                 dateTime.getDate() +
                 "/" +
                 month +
                 "/" +
                 dateTime.getFullYear();
             var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
             var hours = dateTime.getHours() % 12 || 12;
             let time =
                 hours +
                 ":" +
                 dateTime.getMinutes() +
                 ":" +
                 dateTime.getSeconds() +
                 " " +
                 AmOrPm;

             return [date, time];
         }
     },
 }


