                <img
                  src="https://youreats.co.uk/public/front/images/empty-cart.png"
                  class="img-fluid"
                />
                <div class="mt-4">
                  <h6 class="text-dark">Cart Empty</h6>
                </div>
              </div>
              <div class="col-md-4">
                <cartRightPanel ref="cartRightPanel"></cartRightPanel>
