</template>

<script>
import axios from "axios";
import Vue from "vue";
import _ from "lodash";
import $ from "jquery";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith,
  thresholdShippingCost
} from "@/store/variable";
import store from "@/store/store";
import cartRightPanel from "./EditCartRightPanel.vue";
import Loader from "@/components/Loader";
import firebase from "firebase";

export default {
  name: "EditOrderDashboard",

  components: {
    cartRightPanel,
    Loader
  },

  data() {
    return {
      orderId: null,
      searchInput: null,
      loading: false,
      itemsOrCart: true,
      items: [],
      selectedAvailbilityOfItem: [],
      cart: [],
      subTotal: 0,
      totalPayableAmount: 0,
      shippingCharges: 0,
      placedFrom: 2,
      userId: null,
      deliveryAddress: 0,

      success: false,
      message: null
    };
  },

  created() {
    window.addEventListener("beforeunload", function(event) {
      event.returnValue =
        "You have some items in cart. Leaving might loose them.Wanna Stay?";
    });
  },

  mounted() {
    setTimeout(() => {
      this.$refs.cartRightPanel.placedFrom = this.placedFrom;
      this.$refs.cartRightPanel.ordererId = this.userId;
      this.$refs.cartRightPanel.deliveryAddress = this.deliveryAddress;

      console.log(this.userId);
      if (this.userId) {
        this.$refs.cartRightPanel.searchOrderer(this.userId);
      }
    }, 2000);
  },

  beforeDestroy() {
    window.removeEventListener("beforeunload", function(event) {
      event.returnValue = undefined;
    });
  },

  beforeRouteEnter(to, from, next) {
    let url = domain + "order";
    axios
      .get(url, {
        params: {
          id: to.params.orderId,
          products: "budhbak"
        }
      })
      .then(res => {
        res.data.forEach(prod => {
          // putting data
          next(vm => {
            vm.orderId = prod.id;
            vm.subTotal = prod.totalDiscountedAmount;
            vm.totalPayableAmount = prod.totalPayableAmount;
            vm.shippingCharges = prod.deliveryCharges;
            vm.placedFrom = prod.placedFrom;
            vm.ordererId = prod.userId;
            vm.deliveryAddress = prod.deliveryAddress;
            vm.placedFrom = prod.placedFrom;
            vm.userId = prod.userId;
            vm.deliveryAddress = prod.deliveryAddress;

            //products
            prod.products.forEach(doc => {
              let inCart = false;
              var item = new Array();
              // image name
              let imageName = null;
              if (doc.image) {
                let start = doc.image.indexOf("items%2F") + 8;
                let end = doc.image.indexOf("?");
                imageName = doc.image.substring(start, end);
                imageName = vm.truncate(imageName);
              }
              item = {
                productId: doc.productId,
                itemId: doc.itemId,
                name: doc.name,
                cat: doc.allcat,
                stock: doc.stock,
                unit: doc.unit,
                mrp: doc.pivot.MRP,
                currentPrice: doc.pivot.currentPrice,
                discount: doc.pivot.discount,
                image: doc.image,
                imageName: imageName,
                qty: doc.qty,
                quantityToBuy: doc.pivot.quantity,
                priceToBuy: 0,
                inCart: true,
                item_brand: doc.item_brand,
                active: doc.active
              };
              vm.cart.push(item);
            });
          });
        });
      })
      .catch(err => {
        console.log(err.message);
      });
  },

  beforeRouteLeave(to, from, next) {
    if (this.cart.length > 0) {
      if (
        confirm(
          "You have some items in cart. Leaving might loose them.Wanna Leave?"
        )
      ) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },

  watch: {
    $route: "fetchOrder"
  },

  methods: {
    editOrder(ordererId, deliveryAddress) {
      let orderedItems = null;
      let quantity = "";

      this.cart.forEach((item, index) => {
        if (orderedItems == null) {
          orderedItems = item.productId;
        } else {
          orderedItems = orderedItems + "," + item.productId;
        }

        if (quantity == null || quantity == "") {
          quantity = item.quantityToBuy;
        } else {
          quantity = quantity + "," + item.quantityToBuy;
        }
      });

      let url = domain + "order/" + this.orderId;
      axios
        .post(
          url,
          {
            _method: "PUT",
            orderedItems: orderedItems,
            quantity: quantity,
            deliveryAddress: deliveryAddress
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          if (res.data) {
            //order placed ka message

            this.items = [];
            this.subTotal = 0;
            this.totalPayableAmount = 0;
            this.shippingCharges = 0;
            this.$refs.cartRightPanel.deliveryAddress = 0;
            this.$refs.cartRightPanel.ordererId = null;
            this.$refs.cartRightPanel.ordererFound = false;
            this.$refs.cartRightPanel.deliveryAddress = [];

            this.message = "Order has been Updated successfully";
            this.success = true;
            this.loading = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          }
        })
        .catch(err => {
          this.message = "Some error occured";
          this.success = true;
          this.loading = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        });
    },

    addToCart(index) {
      //adding to cart
      if (
        this.items[index][this.selectedAvailbilityOfItem[index]].quantityToBuy >
        0
      ) {
        if (
          this.items[index][this.selectedAvailbilityOfItem[index]].inCart ==
          false
        ) {
          this.cart.unshift(
            this.items[index][this.selectedAvailbilityOfItem[index]]
          );
          Vue.set(
            this.items[index][this.selectedAvailbilityOfItem[index]],
            "inCart",
            true
          );
        } else {
          this.cart.forEach(element => {
            if (
              element.productId ==
              this.items[index][this.selectedAvailbilityOfItem[index]].productId
            ) {
              element = this.items[index][
                this.selectedAvailbilityOfItem[index]
              ];
            }
          });
        }
        this.total();
      } else {
        if (
          this.items[index][this.selectedAvailbilityOfItem[index]]
            .quantityToBuy == 0
        ) {
          alert("Please add some quantity to add it in cart");
        }
      }
    },

    calculateQuantity(index, type) {
      if (type == "items") {
        var quantity = (
          this.items[index][this.selectedAvailbilityOfItem[index]].priceToBuy /
          this.items[index][this.selectedAvailbilityOfItem[index]].currentPrice
        ).toFixed(2);

        if (
          this.items[index][this.selectedAvailbilityOfItem[index]].stock <
          quantity
        ) {
          if (
            confirm(
              "Only " +
                this.items[index][this.selectedAvailbilityOfItem[index]].stock +
                " are available right now"
            )
          ) {
            quantity = this.items[index][this.selectedAvailbilityOfItem[index]]
              .stock;
          } else {
            quantity = 0;
          }
        }

        Vue.set(
          this.items[index][this.selectedAvailbilityOfItem[index]],
          "quantityToBuy",
          quantity
        );
      } else {
        var quantity = (
          this.cart[index].priceToBuy / this.cart[index].currentPrice
        ).toFixed(2);

        if (this.cart[index].stock < quantity) {
          if (
            confirm(
              "Only " + this.cart[index].stock + " are available right now"
            )
          ) {
            quantity = this.cart[index].stock;
          } else {
            quantity = 0;
          }
        }

        Vue.set(this.cart[index], "quantityToBuy", quantity);
      }
    },

    changeQuantity(index, array) {
      if (array == "cart") {
        this.total();
      }
    },

    copyProductId(id) {
      let prodID = "#" + id;
      let idToCopy = document.querySelector(prodID);
      idToCopy.setAttribute("type", "text");
      idToCopy.select();

      try {
        let successful = document.execCommand("copy");
        let message = successful ? "Copied!" : "Unable to Copy!";
        alert(message);
      } catch (err) {
        alert("Whoops! unable to copy");
      }

      idToCopy.setAttribute("type", "hidden");
      window.getSelection().removeAllRanges();
    },

    fetchOrder() {
      console.log("idhr aaya apun chitya");
      let url = domain + "order";
      axios
        .get(url, {
          params: {
            id: this.$route.params.orderId,
            products: "budhbak"
          }
        })
        .then(res => {
          res.data.forEach(prod => {
            // putting data
            this.orderId = prod.id;
            this.subTotal = prod.totalDiscountedAmount;
            this.totalPayableAmount = prod.totalPayableAmount;
            this.shippingCharges = prod.deliveryCharges;
            this.$refs.cartRightPanel.placedFrom = prod.placedFrom;
            this.$refs.cartRightPanel.ordererId = prod.userId;
            this.$refs.cartRightPanel.deliveryAddress = prod.deliveryAddress;

            this.placedFrom = prod.placedFrom;
            this.userId = prod.userId;
            this.deliveryAddress = prod.deliveryAddress;

            //products
            prod.products.forEach(doc => {
              let inCart = false;
              var item = new Array();
              // image name
              let imageName = null;
              if (doc.image) {
                let start = doc.image.indexOf("items%2F") + 8;
                let end = doc.image.indexOf("?");
                imageName = doc.image.substring(start, end);
                imageName = this.truncate(imageName);
              }
              item = {
                productId: doc.productId,
                itemId: doc.itemId,
                name: doc.name,
                cat: doc.allcat,
                stock: doc.stock,
                unit: doc.unit,
                mrp: doc.MRP,
                currentPrice: doc.currentPrice,
                discount: doc.discount,
                image: doc.image,
                imageName: imageName,
                qty: doc.qty,
                quantityToBuy: 0,
                priceToBuy: 0,
                inCart: true,
                item_brand: doc.item_brand,
                active: doc.active
              };
              this.cart.push(item);
            });
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    },

    async placeOrder() {
      console.log("aaya");
      this.loading = true;
      let deliveryAddress = this.$refs.cartRightPanel.deliveryAddress;
      let ordererId = this.$refs.cartRightPanel.ordererId;

      if (deliveryAddress < 0 && ordererId) {
        //add user first
        await this.$refs.cartRightPanel
          .addAddress(ordererId)
          .then(res => {
            deliveryAddress = res.data.id;
          })
          .catch(err => {
            this.message = "There's some problem in storing address";
            this.success = true;
            this.loading = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }

      if (deliveryAddress > 0 && ordererId) {
        this.editOrder(ordererId, deliveryAddress);
      } else {
        this.disable = false;
        if (ordererId == null || ordererId == "") {
          alert("Have you checked the user first?");
        } else if (deliveryAddress < 0 && ordererId) {
          alert("Please select a valid address");
        }
      }
    },

    removeFromCart(index) {
      this.items.forEach(element => {
        element.forEach(item => {
          if (item.productId == this.cart[index].productId) {
            Vue.set(item, "quantityToBuy", 0);
            Vue.set(item, "inCart", false);
          }
        });
      });
      this.cart.splice(index, 1);
      this.total();
    },

    searchItems() {
      this.loading = true;
      this.items = [];

      // loading content
      let url = domain + "search";

      axios
        .get(url, {
          params: {
            data: this.searchInput,
            table: "items",
            offset: "0",
            category: "fetch"
          },
          headers: {
            from: "webappOrder"
          }
        })
        .then(res => {
          if (res.data) {
            this.items = [];
            this.selectedAvailbilityOfItem = [];
            res.data.forEach(doc => {
              {
                var itemArray = new Array();
                let itemUrl = domain + "item";
                axios
                  .get(itemUrl, {
                    params: {
                      similarProductId: doc.similarProductId
                    }
                  })
                  .then(query => {
                    if (query.data) {
                      query.data.forEach(similarDoc => {
                        {
                          let inCart = false;
                          this.cart.forEach(element => {
                            if (element.productId == similarDoc.productId) {
                              inCart = true;
                            }
                          });
                          var item = new Array();
                          // image name
                          let imageName = null;
                          if (similarDoc.image) {
                            let start =
                              similarDoc.image.indexOf("items%2F") + 8;
                            let end = similarDoc.image.indexOf("?");
                            imageName = similarDoc.image.substring(start, end);
                            imageName = this.truncate(imageName);
                          }
                          item = {
                            productId: similarDoc.productId,
                            itemId: similarDoc.itemId,
                            name: similarDoc.name,
                            cat: similarDoc.allcat,
                            stock: similarDoc.stock,
                            unit: similarDoc.unit,
                            mrp: similarDoc.MRP,
                            currentPrice: similarDoc.currentPrice,
                            discount: similarDoc.discount,
                            image: similarDoc.image,
                            imageName: imageName,
                            qty: similarDoc.qty,
                            uploadedBy: similarDoc.uploadedBy,
                            uploadedAt: similarDoc.uploadedAt,
                            cocatName: doc.cocategory.name,
                            cocatId: doc.cocategory.coid,
                            subcatName: doc.cocategory.subcategory.name,
                            subcatId: doc.cocategory.subcategory.subcatid,
                            catName: doc.cocategory.subcategory.category.name,
                            quantityToBuy: 0,
                            priceToBuy: 0,
                            inCart: inCart,
                            item_brand: similarDoc.item_brand,
                            active: similarDoc.active
                          };

                          itemArray.push(item);
                        }
                      });
                      this.items.push(itemArray);
                      this.selectedAvailbilityOfItem.push(0);
                    }
                  });
              }
            });

            this.loading = false;
          }
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
        });
    },

    selectSize(index, innerIndex) {
      Vue.set(this.selectedAvailbilityOfItem, index, innerIndex);
    },

    total() {
      this.subTotal = 0;
      this.shippingCharges = 0;
      this.totalPayableAmount = 0;
      this.cart.forEach(doc => {
        this.subTotal = this.subTotal + doc.quantityToBuy * doc.currentPrice;
      });

      this.subTotal = parseFloat(this.subTotal.toFixed(2));

      if (this.subTotal > thresholdShippingCost && this.subTotal > 0) {
        this.shippingCharges = 0;
      } else {
        this.shippingCharges = 35;
      }

      this.shippingCharges = parseFloat(this.shippingCharges.toFixed(2));

      this.totalPayableAmount = parseFloat(this.subTotal + this.shippingCharges);
    },

    truncate(value) {
      const length = 10;
      return value.length <= length
        ? value
        : value.substring(0, length) + "...";
    }
  }
};
</script>

<style scoped>
.search_icon {
  height: 40px;
  width: 40px;
  float: right;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: white;
}

.search-bar {
  background: #eae7e7;
}

.search-bar:focus {
  background: #eeeeee;
}

.cart {
  position: relative;
}

.cart-items {
  position: absolute;
  top: 0;
  right: 0.5px;
}

.table-fix-size {
  max-height: 10px;
  height: 10px;
  overflow-y: auto;
}
</style>



