<template>
  <div>
    <div class="row pt-2">
      <div class="col-6">
        <form class="form-inline">
          <div class="input-group content-search border">
            <div class="input-group-prepend">
              <span class="input-group-text" id="basic-addon1">
                <i class="fa fa-search"></i>
              </span>
            </div>
            <input
              type="text"
              v-model.number="search"
              class="form-control hz-input"
              placeholder="Search by Number Here..."
              aria-label="Search by Number Here..."
              aria-describedby="basic-addon1"
              @blur.prevent="searching"
            />
          </div>
        </form>
      </div>
      <div class="col-6">
        <!-- order select not for return, cancelled orders -->
        <form
          class="form-inline float-right"
          v-if="status && (status==3 || status==4 || status==5 || status==7)"
        >
          <span class="font-weight-semibold pr-3">Select Order Status</span>
          <select
            v-model="action"
            name="status"
            class="custom-select"
            @change="updateAction()"
          >
            <option disabled="disabled" selected="selected">Choose Action</option>
            <!-- <option value="1">Cancel</option> -->
            <option value="1" v-if="status==3 || status==4 || status==5 || status==6">Cancel</option>
            <option value="2" v-if="status==7">Request Return</option>
            <option value="4" v-if="status==3">Processing</option>
            <option value="5" v-if="status==4">Packed</option>
            <option value="d5" v-if="status==5">Ready For Pickup</option>
            <!-- <option value="6" v-if="status==5">Out For Delivery</option> -->
            <!-- <option value="7" v-if="status==6">Delivered</option> -->
            <option value="9" v-if="status==2">Return Approved</option>
            <!-- <option value="10" v-if="status==9">Returned</option> -->
            <option value="8">Failed</option>
            <option value="delete">Delete</option>
            <!-- <option
              :value="status.id"
              v-for="status in orderStatus"
              :key="status.id"
            >{{status.status}}</option>-->
          </select>
        </form>
      </div>
    </div>
    <div class="table-responsive table-scroll mt-4">
      <table class="table table-hover table-sm table-borderless table-content text-left">
        <thead>
          <tr>
            <th v-if="status && (status==3 || status==4 || status==5 || status==7)">
              <form>
                <div class="custom-control custom-checkbox">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    id="customCheck"
                    @click="selectAll"
                    v-model="select"
                  />
                  <label class="custom-control-label" for="customCheck"></label>
                </div>
              </form>
            </th>
            <th>Order Id</th>
            <th>Order By</th>
            <th>Products</th>
            <th>Total Items</th>
            <th>Payment Mode</th>
            <th>Payment Status</th>
            <th>Date</th>
            <th>Amount</th>
            <th>Order Status</th>
            <th v-if="status==1 || status==2 || status==10 || status==9">Reason</th>
            <th v-if="status==1 || status==10">Transactions</th>
            <th>Placed From</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody v-if="orders.length>0">
          <tr v-for="(order,index) in orders" :key="order.id">
            <td v-if="status && (status==3 || status==4 || status==5 || status==7)">
              <form>
                <div class="custom-control custom-checkbox">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    :id="order.orderId"
                    v-model="order.checked"
                    :value="order.orderId"
                  />
                  <label class="custom-control-label" :for="order.orderId"></label>
                </div>
              </form>
            </td>
            <td>
              {{order.orderId.substring(0,4)+"..."}}
              <i
                class="far fa-copy pl-1"
                style="cursor:pointer"
                @click="copyProductId(order.orderId)"
              ></i>
            </td>
            <td>
              {{order.deliveryAddress.name}}
              <br />
              {{order.user.mobileNumber}}
            </td>
            <td>
              <div v-if="order.products.length>2">
                <span v-for="product in order.products.slice(0,2)" :key="product.productId">
                  <img :src="product.image" class="img-fluid content-image" /> +
                </span>
                <span>
                  <b>{{order.products.length-2}} more</b>
                </span>
              </div>
              <div v-else>
                <span v-for="product in order.products" :key="product.productId">
                  <img :src="product.image" class="img-fluid content-image" />
                </span>
              </div>
            </td>
            <td>{{order.orderedItemsCount}}</td>
            <td>
              <span class="text-dark">{{order.paymentMode.mode}}</span>
            </td>
            <td>
              <span class="bagde badge-waiting pl-1 pr-1" v-if="order.paidAmount==0">Not paid</span>
              <span
                class="bagde badge-paid pl-1 pr-1"
                v-else-if="order.paidAmount==order.payment"
              >Paid</span>

              <span class="bagde badge-failed pl-1 pr-1" v-else>Pending</span>
            </td>
            <td>
              <span class="text-dark">{{order.orderDate}}</span>
              <br />
              <small>{{order.orderTime}}</small>
            </td>
            <td class="text-dark">{{order.payment}}</td>
            <td>
              <span
                class="badge badge-cancelled pl-2 pr-2"
                v-if="order.orderStatus.id==1"
              >{{order.orderStatus.status}}</span>
              <span
                class="badge badge-request-return pl-2 pr-2"
                v-else-if="order.orderStatus.id==2"
              >{{order.orderStatus.status}}</span>
              <span
                class="badge badge-processing pl-2 pr-2"
                v-else-if="order.orderStatus.id==4"
              >{{order.orderStatus.status}}</span>
              <span
                class="badge badge-packed pl-2 pr-2"
                v-else-if="order.orderStatus.id==5"
              >{{order.orderStatus.status}}</span>
              <span
                class="badge badge-shipping pl-2 pr-2"
                v-else-if="order.orderStatus.id==6"
              >{{order.orderStatus.status}}</span>
              <span
                class="badge badge-delivered pl-2 pr-2"
                v-else-if="order.orderStatus.id==7"
              >{{order.orderStatus.status}}</span>
              <span
                class="badge badge-failed pl-2 pr-2"
                v-else-if="order.orderStatus.id==8"
              >{{order.orderStatus.status}}</span>
              <span
                class="badge badge-request-return pl-2 pr-2"
                v-else-if="order.orderStatus.id==9"
              >{{order.orderStatus.status}}</span>
              <span
                class="badge badge-return pl-2 pr-2"
                v-else-if="order.orderStatus.id==10"
              >{{order.orderStatus.status}}</span>
            </td>
            <td v-if="order.orderBack">{{order.orderBack.reason}}</td>
            <td v-if="status==1 || status==10">
              <div v-if="order.transactions.length>0">
                <span
                  v-for="transaction in order.transactions"
                  :key="transaction.transactionId"
                >{{transaction.transactionId}} - {{transaction.amount}}</span>
              </div>
              <span v-else>No transaction</span>
            </td>
            <td>
              <span v-if="order.placedFrom==1">App</span>
              <span v-if="order.placedFrom==2">WhatsApp</span>
              <span v-if="order.placedFrom==3">Call</span>
              <span v-if="order.placedFrom==4">SMS</span>
            </td>

            <td class="text-dark">
              <div>
                <router-link :to="{name: 'ViewOrder', params: {orderId: order.orderId}}">
                  <i class="fa fa-eye"></i>
                </router-link>
                <router-link :to="{name: 'OrderEdit', params: {orderId: order.orderId}}">
                  <i class="fa fa-pencil-alt pl-2"></i>
                </router-link>
              </div>
              <div>
                <router-link
                  :to="{name: 'OrderReturnSlip', params: {slipType:'returnSlip',orderId: order.orderId}}"
                  tag="a"
                  target="_blank"
                  v-if="status==2 || status==9"
                >Generate Return Slip</router-link>
                <router-link
                  :to="{name: 'OrderBill', params: {slipType:'bill',orderId: order.orderId}}"
                  tag="a"
                  target="_blank"
                  v-else
                >Generate Bill</router-link>
              </div>
              <div v-if="status==1 || status==10">
                <br />
                <span v-if="order.refundId">Refunded - {{order.refund.amount}}</span>
                <span v-else-if="order.paidAmount==0">Refund Not Possible</span>
                <button
                  v-else
                  data-toggle="modal"
                  data-target="#refundModal"
                  @click="refund(index)"
                >Make refund</button>
              </div>
            </td>
          </tr>
        </tbody>
        <tbody v-else>
          <tr>
            <td colspan="9" class="text-center">
              <div>
                <img
                  class="card-img-top img-fluid"
                  src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                  alt="Card image cap"
                  style="width:20em"
                />
                <div class="card-body">
                  <p class="card-text">Oops! There ain't any orders here</p>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <!--refund modal -->
    <div
      class="modal fade"
      id="refundModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="refundModalLabel"
      aria-hidden="true"
      v-if="refundOrder"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Refund</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <span>Refunding to {{refundOrder.user.name}}</span>
            <form class="form">
              <!-- payment mode-->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Payment Mode</label>
                  {{refundPaymentMode}}
                  <select
                    v-model="refundPaymentMode"
                    class="form-control form-control-sm"
                    :disabled="disable"
                  >
                    <option
                      :value="paymentMode.id"
                      class="font-weight-bold"
                      v-for="paymentMode in paymentModes"
                      :key="paymentMode.id"
                    >{{paymentMode.mode}}</option>
                  </select>
                </div>
              </div>
              <!-- amount -->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Amount</label>
                  <input
                    type="number"
                    v-model.number="refundAmount"
                    class="form-control form-control-sm"
                    required
                    :max="refundOrder.paidAmount"
                    min="0"
                    :disabled="disable"
                  />
                  <small>Max Amount: {{refundOrder.paidAmount}}</small>
                </div>
              </div>
              <!-- transaction Id-->
              <div class="form-row" v-if="refundPaymentMode!=1 && refundPaymentMode!=7">
                <div class="col-sm-12">
                  <label>Transaction Id</label>
                  <input
                    type="text"
                    v-model="refundTransactionId"
                    class="form-control form-control-sm"
                    required
                    :disabled="disable"
                  />
                  <!-- <span class="error text-danger font-weight-bold" v-if="errors[1]">{{errors[1]}}</span> -->
                </div>
              </div>
              <!-- transaction date/time-->
              <div class="form-row" v-if="refundPaymentMode!=1 && refundPaymentMode!=7">
                <div class="col-sm-12">
                  <datetime v-model="refundTransactionTime" type="datetime">
                    <label for="expiriesAt" slot="before">Transaction Time</label>
                    <small class="description" slot="before">(When transaction is made)</small>
                  </datetime>
                  <!-- <span class="error text-danger font-weight-bold" v-if="errors[1]">{{errors[1]}}</span> -->
                </div>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-primary" @click="refundingOrder()">Refund</button>
          </div>
        </div>
      </div>
    </div>
    <!-- refund modal ends -->

    <OrderFooter />
    <Loader :disable="loading" :success="success" :message="message" />
  </div>
</template>

<script>
import axios from "axios";
import firebase from "firebase";
import Vue from "vue";
import { bus } from "../../../main";
import Loader from "../../Loader";
import OrderFooter from "./OrderFooter.vue";
import { domain, utcHours, utcMins, ContentType, AccessControlAllowOrigin, XRequestedWith } from "@/store/variable";
import { Datetime } from "vue-datetime";
import store from "@/store/store";
export default {
  name: "DashBoard",
  components: {
    OrderFooter,
    Loader,
    datetime: Datetime
  },
  props: {
    status: Number,
    newOrders: Array,
    disable: Boolean
  },

  data() {
    return {
      orderStatus: [],
      search: null,

      select: false,
      action: null,

      message: null,
      success: null,
      loading: this.disable,

      paymentModes: [],
      refundTransactionId: null,
      refundAmount: 0,
      refundPaymentMode: 7,
      refundTransactionTime: null,
      refundOrder: null,
      refundIndex: 0
    };
  },

  computed: {
    orders() {
      return this.newOrders;
    },

    user() {
      return store.state.user;
    }
  },

  created() {
    this.getOrderStatus();

    bus.$on("paginateOrder", data => {
      this.paginateOrder(data["page"]);
    });
  },

  methods: {
    showModal() {
      this.isRefundModalVisible = true;
    },
    closeModal() {
      this.isRefundModalVisible = false;
    },

    copyProductId(id) {
      let prodID = "#" + id;
      let idToCopy = document.querySelector(prodID);
      idToCopy.setAttribute("type", "text");
      idToCopy.select();

      try {
        let successful = document.execCommand("copy");
        let message = successful ? "Copied!" : "Unable to Copy!";
        alert(message);
      } catch (err) {
        alert("Whoops! unable to copy");
      }

      idToCopy.setAttribute("type", "hidden");
      window.getSelection().removeAllRanges();
    },

    updateAction() {
      if (this.user.designation == "admin") {
        this.orders.forEach((order, index) => {
          if (order.checked) {
            if (this.action == "delete") {
              if (confirm("You are about to delete " + order.orderId)) {
                this.loading = true;
                let url = domain + "order/" + order.orderId;
                axios
                  .post(
                    url,
                    {
                      _method: "DELETE"
                    },
                    {
                      withCredentials: true,
                      headers: {
                        "Content-Type": "application/json",
                        "X-Requested-With": "XMLHttpRequest",
                        "X-api-token": firebase.auth().currentUser.uid
                      }
                    }
                  )
                  .then(res => {
                    if (res.data) {
                      this.message = "Order successfully deleted";
                      this.success = true;
                      this.loading = false;
                      this.action = null;

                      this.orders.splice(index, 1);

                      setTimeout(() => {
                        this.success = false;
                        this.message = null;
                      }, 2000);
                    }
                  })
                  .catch(err => {
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  });
              }
            } else if (this.action == "d5") {
              var action = 3;
              let url = domain + "delivery";
              this.loading = true;
              axios
                .get(url, {
                  params: {
                    orderId: order.orderId
                  }
                })
                .then(res => {
                  let url = domain + "delivery/" + res.data[0].id;
                  axios
                    .post(
                      url,
                      {
                        _method: "PUT",
                        status: action
                      },
                      {
                        withCredentials: true,
                        headers: {
                          "Content-Type": "application/json",
                          "X-Requested-With": "XMLHttpRequest",
                          "X-api-token": firebase.auth().currentUser.uid
                        }
                      }
                    )
                    .then(res => {
                      this.message = res.data;
                      this.success = true;
                      this.loading = false;
                      this.action = null;

                      var status = {};
                      this.orderStatus.forEach(state => {
                        if (state.id == this.action) {
                          status = state;
                        }
                      });

                      //colsing inputs
                      Vue.set(this.orders[index], "orderStatus", status);

                      setTimeout(() => {
                        this.success = false;
                        this.message = null;
                      }, 2000);
                    })
                    .catch(err => {
                      this.message = "Some error occured";
                      this.success = true;
                      this.loading = false;
                      this.action = null;

                      setTimeout(() => {
                        this.success = false;
                        this.message = null;
                      }, 2000);
                    });
                });
            } else if (this.action == "1" || this.action == "2") {
              this.loading = true;
              let url = domain + "order/" + order.orderId;
              axios
                .post(
                  url,
                  {
                    _method: "PUT",
                    reason: "Done from webapp",
                    preferredPaymentMode: 1,
                    status: this.action
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": ContentType,
                      "X-Requested-With": XRequestedWith,
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  this.message = res.data;
                  this.success = true;
                  this.loading = false;
                  this.action = null;

                  var status = {};
                  this.orderStatus.forEach(state => {
                    if (state.id == this.action) {
                      status = state;
                    }
                  });

                  //colsing inputs
                  Vue.set(this.orders[index], "orderStatus", status);
                  console.log(res.data);
                  if (res.data == "Success") {
                    this.orders.slice(index, 1);
                  }

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                })
                .catch(err => {
                  this.message = "Some error occured";
                  this.success = true;
                  this.loading = false;
                  this.action = null;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
            } else {
              this.loading = true;
              let url = domain + "order/" + order.orderId;
              axios
                .post(
                  url,
                  {
                    _method: "PUT",
                    status: this.action
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": "application/json",
                      "X-Requested-With": "XMLHttpRequest",
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  this.message = res.data;
                  this.success = true;
                  this.loading = false;
                  this.action = null;

                  var status = {};
                  this.orderStatus.forEach(state => {
                    if (state.id == this.action) {
                      status = state;
                    }
                  });

                  //colsing inputs
                  Vue.set(this.orders[index], "orderStatus", status);

                  console.log(res.data);
                  if (res.data == "Success") {
                    console.log("aaya");
                    this.orders.slice(index, 1);
                  }

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                })
                .catch(err => {
                  this.message = "Some error occured";
                  this.success = true;
                  this.loading = false;
                  this.action = null;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
            }
          }
        });
      } else {
        confirm("You are not allowed to make changes");
      }
    },

    getOrderStatus() {
      let url = domain + "order/status";
      axios
        .get(url)
        .then(res => {
          this.orderStatus = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },

    paginateOrder(page) {
      this.$emit("fetchOrders", page);
    },

    refund(index) {
      this.refundIndex = index;
      this.refundOrder = this.orders[index];
      if (this.refundOrder.orderBack) {
        this.refundPaymentMode = this.refundOrder.orderBack.preferredPaymentMode;
      } else {
        this.refundPaymentMode = 7;
      }
      this.paymentModes = [];
      let url = domain + "payment/mode";
      axios
        .get(url, {
          headers: {
            "X-api-token": "itsaMatch"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            this.paymentModes.push(doc);
          });
        })
        .catch(err => {
          console.log("Payment Modes can't be fetched");
        });
    },

    refundingOrder() {
      if (this.user.designation == "admin" && this.refundAmount > 0) {
        this.loading = true;
        let url = domain + "order/" + this.refundOrder.orderId;
        axios
          .post(
            url,
            {
              _method: "PUT",
              refund: "yes",
              refundAmount: this.refundAmount,
              refundPaymentMode: this.refundPaymentMode,
              refundTransactionId: this.refundTransactionId,
              refundTransactionTime: this.refundTransactionTime
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": "application/json",
                "X-Requested-With": "XMLHttpRequest",
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.data == "Success") {
              this.message = "Success";
              this.refundOrder = null;
              this.order[this.refundIndex].refund = {
                amount: this.refundAmount
              };
            } else {
              this.message = "Some Error Occured";
            }
            this.success = true;
            this.loading = false;

            var status = {};

            //colsing inputs
            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          })
          .catch(err => {
            this.message = "Some error occured";
            this.success = true;
            this.loading = false;
            console.log(err.message);

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    },

    searching() {
      if (this.search && this.search.toString().length == 10) {
        let url = domain + "order";
        let userUrl = domain + "user";

        this.loading = true;
        axios
          .get(userUrl, {
            params: {
              mobileNumber: this.search
            }
          })
          .then(user => {
            if (user.data[0] && user.data[0].uuid) {
              let ordersList = [];

              if (this.status) {
                axios
                  .get(url, {
                    params: {
                      status: this.status,
                      userId: user.data[0].uuid,
                      user: "budhbak",
                      products: "budhbak",
                      payment_mode: "budhbak",
                      userAddress: "budhbak",
                      order: "updated_at",
                      orderWay: "desc"
                    }
                  })
                  .then(res => {
                    res.data.forEach(doc => {
                      let dateTime = new Date(doc.orderTime);
                      dateTime.setHours(dateTime.getHours() + utcHours);
                      dateTime.setMinutes(dateTime.getMinutes() + utcMins);
                      let date =
                        dateTime.getDate() +
                        "/" +
                        dateTime.getMonth() +
                        "/" +
                        dateTime.getFullYear();
                      var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
                      var hours = dateTime.getHours() % 12 || 12;
                      let time =
                        hours +
                        ":" +
                        dateTime.getMinutes() +
                        ":" +
                        dateTime.getSeconds() +
                        " " +
                        AmOrPm;

                      let order = {
                        orderId: doc.id,
                        products: doc.products,
                        orderedItemsCount: doc.orderedItemsCount,
                        paymentMode: doc.payment_mode,
                        orderDate: date,
                        orderTime: time,
                        payment: doc.totalPayableAmount,
                        paidAmount: doc.paidAmount,
                        orderStatus: doc.order_status,
                        user: doc.user,
                        deliveryAddress: doc.user_address,
                        refundId: doc.refundId,
                        checked: false
                      };
                      ordersList.push(order);

                      store.dispatch("patchorderPageCount", 1);
                      store.dispatch("patchorderPageNumber", 1);
                      store.dispatch("patchtotalOrders", res.data.total);
                    });
                  });
              } else {
                axios
                  .get(url, {
                    params: {
                      userId: user.data[0].uuid,
                      user: "budhbak",
                      products: "budhbak",
                      payment_mode: "budhbak",
                      userAddress: "budhbak",
                      order: "updated_at",
                      orderWay: "desc"
                    }
                  })
                  .then(res => {
                    res.data.forEach(doc => {
                      let dateTime = new Date(doc.orderTime);
                      dateTime.setHours(dateTime.getHours() + utcHours);
                      dateTime.setMinutes(dateTime.getMinutes() + utcMins);
                      let date =
                        dateTime.getDate() +
                        "/" +
                        dateTime.getMonth() +
                        "/" +
                        dateTime.getFullYear();
                      var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
                      var hours = dateTime.getHours() % 12 || 12;
                      let time =
                        hours +
                        ":" +
                        dateTime.getMinutes() +
                        ":" +
                        dateTime.getSeconds() +
                        " " +
                        AmOrPm;

                      let order = {
                        orderId: doc.id,
                        products: doc.products,
                        orderedItemsCount: doc.orderedItemsCount,
                        paymentMode: doc.payment_mode,
                        orderDate: date,
                        orderTime: time,
                        payment: doc.totalPayableAmount,
                        paidAmount: doc.paidAmount,
                        orderStatus: doc.order_status,
                        user: doc.user,
                        deliveryAddress: doc.user_address,
                        refundId: doc.refundId,
                        checked: false
                      };
                      ordersList.push(order);

                      store.dispatch("patchorderPageCount", 1);
                      store.dispatch("patchorderPageNumber", 1);
                      store.dispatch("patchtotalOrders", res.data.total);
                    });
                  });
              }

              this.newOrders = ordersList;
              this.loading = false;
            } else {
              this.loading = false;
            }
          });
      }
    },

    selectAll() {
      this.orders.forEach(order => {
        if (!this.select) {
          order.checked = true;
          //Vue.set(order, checked, true);
        } else {
          order.checked = false;
        }
      });
    }
  }
};
</script>



