                    >
                      <option
                        v-for="unit in units"
                        :key="unit.id"
                        :value="unit.value"
                        class="font-weight-bold"
                        >{{ unit.name }} - {{ unit.value }}</option
                      >
                    </select>
                  </div>
                </div>

                <!-- buttons -->
                <div class="form-row">
                  <div class="col text-center">
                    <button
                      class="btn btn-sm btn-go-green font-weight-bold d-inline"
                      type="submit"
                      @click.prevent="add"
                      :disabled="disable"
                    >
                      <i class="fas fa-save pr-1"></i>
                      SAVE
                    </button>
                    <router-link
                      to="/inventory"
                      class="btn btn-sm btn-secondary"
                    >
                      <i class="fas fa-ban pr-1"></i>
                      <div class="font-weight-bold d-inline">CANCEL</div>
                    </router-link>
                  </div>
                </div>

                <div class="form-row" v-if="errors[14]">
                  <div class="col text-center text-danger">
                    {{ errors[14] }}
                  </div>
                </div>
              </form>
            </div>
          </div>
          <!-- modal -->
          <div class="modal" tabindex="-1" role="dialog" id="profitLossModal">
            <div class="modal-dialog modal-dialog-centered" role="document">
              <div class="modal-content">
                <div class="modal-header">
                  <h6 class="modal-title">
                    <i class="fas fa-exclamation-circle text-danger"></i>
                    <div class="d-inline font-weight-bold pl-2 text-danger">
                      Alert! Proceed with precaution
                    </div>
                  </h6>
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div class="modal-body">
                  <strong style="opacity:0.75">
                    Are you sure you wanna upload
                    <span class="font-weight-bold text-success"
                      > {{ currentPrice }}</span
                    >
                    as current price ?
                  </strong>
                  <p class="mt-3">
                    This will incur a
                    <span class="text-danger font-weight-bold">
                      loss of 
                      {{ this.profitLoss }}
                    </span>
                    that is
                    <span class="font-weight-bold text-danger"
                      >{{ profitLossPer }}%</span
                    >
                    to doorstep
                  </p>
                </div>
                <div class="modal-footer">
                  <div class="mr-auto">
                    <a href="#" class="font-weight-bold text-dark">
                      <i class="fas fa-question pr-1"></i>Get Help
                    </a>
                  </div>
                  <button
                    type="button"
                    class="btn btn-secondary btn-sm font-weight-bold"
                    data-dismiss="modal"
                  >
                    <i class="fas fa-ban pr-2" @click="this.mrp = 0"></i>CANCEL
                  </button>
                  <button
                    type="button"
                    class="btn btn-danger btn-sm font-weight-bold"
                    data-dismiss="modal"
                  >
                    <i class="fas fa-cloud-upload-alt pr-1 font-weight-bold"></i
                    >UPLOAD
                  </button>
                </div>
              </div>
            </div>
          </div>
          <!-- modal end -->
        </div>
        <div class="col-lg-2"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import $ from "jquery";
import Vue from "vue";
import db from "../firebaseInit";
import firebase from "firebase";
import { firestorage } from "../firebaseInit";

import Loader from "../Loader";
import LoaderGif from "../LoaderGif";

import { inventoryMixin } from "../Mixins/Inventory/inventoryMixin.js";

export default {
  name: "addItem",
  mixins: [inventoryMixin],
  components: {
    Loader,
    LoaderGif
  },

  data() {
    return {
      // ckeditor
      editor: ClassicEditor,
      editorData: "<p>Description</p>",
      editorConfig: {
        // The configuration of the editor.
      },
      // for item cats
      ModalContentLoading: false,

      // item details
      active: true,
      itemId: null,
      color: null,

      name: null,
      image: null,
      detailImages: [],
      thumbnail: null,

      unit: null,
      unit2: null,
      barcode: null,

      stock: 0,
      home: "hcat",
      allcat: "acat",
      disPriority: 0,
      description: "<p>Description</p>",
      qty: 0,
      sku: null,
      mfgDate: null,
      expiryDate: null,
      shelfLife: null,
      brand: [],
      address: null,
      ean: 0,
      profitLoss: 0,
      profitLossPer: 0,
      dropShip: false,
      dropShipAddress: null,
      maxLimit: 0,
      ingredients: "<p></p>",
      packingType: null,
      features: "<p></p>",
      similarProductId: 0,

      imageOption: true,
      thumbnailOption: true,
      detailImageOption: true,
      disVal: false,
      message: null,
      disable: false,
      disableMessage: null,
      success: false,
      similarItems: [],
      errors: [],
      duplicate: true,
      othersimilarProduct: false,

      searchingItem: false,
      searchProductID: null,
      enterBrand: false,

      priceBand: 10
    };
  },

  created() {
    this.fetchCategories();
  },
  beforeRouteLeave(to, from, next) {
    if (
      this.name ||
      this.discount > 0 ||
      this.mrp > 0 ||
      this.msp > 0 ||
      this.stock > 0 ||
      this.disPriority > 0 ||
      this.description != "description" ||
      this.qty > 0 ||
      this.maxLimit > 0
    ) {
      if (confirm("Your changes will be lost.Do you want to leave the page?")) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },

  methods: {
    async add() {
      // binging preloader on
      this.duplicate = await this.checkItem();
      let flag = true;

      if (
        this.validate() &&
        this.duplicate == false &&
        (this.user.designation == "admin" ||
          this.user.designation == "webappUploader") &&
          this.user.status == "active"
      ) {
        this.disable = true;
        this.disableMessage = "Uploading Data, Kripya Shanti banaye rkhe...";

        // add brand
        if (this.enterBrand) {
          await this.addBrand();
          if (!isNaN(this.brand)) {
            flag = false;
          }
        }

        if (
          this.media[0] &&
          this.media[0] != null &&
          this.media[0] != undefined &&
          flag == true
        ) {
          if (
            (this.user.designation == "admin" ||
              (this.user.designation == "webappUploader" &&
                this.user.package == 3)) &&
            this.user.status == "active"
          ) {
            await this.uploadImage().then(downloadURL => {
              this.image = downloadURL;
            });
          } else {
            if (this.user.package != 3) {
              alert("Your package does not allow you to upload image");
            } else if (this.user.status != "active") {
              alert("Aap se yeh adikar cheen liya gya hai");
            }
            flag = false;
            this.disable = false;

            return;
          }
        }

        if (
          this.media[1] &&
          this.media[1] != null &&
          this.media[1] != undefined &&
          flag == true
        ) {
          if (
            (this.user.designation == "admin" ||
              (this.user.designation == "webappUploader" &&
                this.user.package == 3)) &&
            this.user.status == "active"
          ) {
            await this.uploadThumbnail().then(downloadURL => {
              this.thumbnail = downloadURL;
            });
          } else {
            if (this.user.package != 3) {
              alert("Your package does not allow you to upload image");
            } else if (this.user.status != "active") {
              alert("Aap se yeh adikar cheen liya gya hai");
            }
            flag = false;
            this.disable = false;

            return;
          }
        }

        if (
          this.media[2] &&
          this.media[2] != null &&
          this.media[2] != undefined &&
          flag == true
        ) {
          if (
            (this.user.designation == "admin" ||
              (this.user.designation == "webappUploader" &&
                this.user.package == 3)) &&
            this.user.status == "active" &&
            this.image != null &&
            this.image != ""
          ) {
            let files = this.$refs.detailImages.files;
            let mediaURL = [];
            this.detailImages = [];
            for (var i = 0; i < files.length; i++) {
              let file = files[i];

              await this.uploadDetailImage(file).then(downloadURL => {
                this.detailImages.push(downloadURL);
              });
            }
          } else {
            if (this.user.package != 3 && this.user.designation != "admin") {
              alert("Your package does not allow you to upload image");
            } else if (this.user.status != "active") {
              alert("Aap se yeh adikar cheen liya gya hai");
            } else if (this.image == null || this.image == "") {
              alert("You need to upload the main image first");
            }
            flag = false;
            this.disable = false;
            return;
          }
        }

        if (flag == true) {
          if (
            (this.image && this.user.package == 3) ||
            this.user.package == 1 ||
            this.user.designation == "admin"
          ) {
            this.uploadData();
          } else {
            if (this.image && this.user.package != 3) {
              alert("Your package does not allow you to upload image");
            } else if (!this.image && this.user.package == 3) {
              alert("Your package demands to upload image with it");
            }
            this.disable = false;
          }
        }
      }
    },

    generateId() {
      let now = new Date();
      let nowTime = now.getTime();
      let nowDate =
        now.getUTCDate() + "" + now.getUTCMonth() + "" + now.getUTCFullYear();
      let id = "xxxxxxxx".replace(/[x]/g, function(c) {
        let r = (nowTime + Math.random() * 8) % 8 | 0;
        nowTime = Math.floor(nowTime / 16);
        return (c === "x" ? r : (r & 0x1) | 0x5).toString(8);
      });

      id =
        "PRO" +
        this.user.uuid.substring(0, 3).toUpperCase() +
        "-" +
        this.user.name.substring(0, 3).toUpperCase() +
        "-" +
        nowDate +
        "-" +
        id;
      return id;
    },

    uploadData() {
      let productId = this.generateId();
      this.sku = this.generateSKU();

      this.name = this.name.toLowerCase().replace(/(\s+.)|^\w/gi, function(v) {
        return v.toUpperCase();
      });
      if (!this.dropShip) {
        this.dropshipAddress = null;
      }

      let url = domain + "item";
      axios
        .post(
          url,
          {
            productId: productId,
            currentPrice: this.currentPrice,
            name: this.processContent("string", this.name),
            image: this.image,
            detailImages: this.detailImages,
            thumbnail: this.thumbnail,
            discount: this.discount,
            ingredients: this.processContent("html", this.ingredients),
            features: this.processContent("html", this.features),
            packingType: this.packingType,
            unit: this.unit,
            unit2: this.unit2,
            color: this.processContent("string", this.color),
            MRP: this.mrp,
            msp: this.msp,
            stock: this.stock,
            home: this.home, // doubted
            allcat: this.allcat, //doubted
            itemId: this.itemId,
            dis_val: this.disVal,
            disPriority: this.disPriority, //doubt
            description: this.processContent("string", this.description),
            qty: this.processContent("string", this.qty),
            sku: this.sku,
            mfgDate: this.processContent("string", this.mfgDate),
            expiryDate: this.processContent("string", this.expiryDate),
            shelfLife: this.processContent("string", this.shelfLife),
            brand: this.brand.name,
            brandName: this.brand.id,
            address: this.processContent("string", this.address),
            ean: this.ean,
            active: this.active,
            dropShipAddress: this.processContent(
              "string",
              this.dropShipAddress
            ),
            similarProductId: this.similarProductId,
            barcode: this.barcode,
            maxLimit: this.maxLimit,
            uploadedAt: new Date(),
            updatedAt: new Date()
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          db.collection("items")
            .doc(productId)
            .set({
              productId: productId,
              currentPrice: this.currentPrice,
              name: this.processContent("string", this.name),
              image: this.image,
              detailImages: this.detailImages,
              thumbnail: this.thumbnail,
              discount: this.discount,
              ingredients: this.processContent("html", this.ingredients),
              features: this.processContent("html", this.features),
              packingType: this.packingType,
              unit: this.unit,
              unit2: this.unit2,
              color: this.processContent("string", this.color),
              MRP: this.mrp,
              msp: this.msp,
              stock: this.stock,
              home: this.home, // doubted
              allcat: this.allcat, //doubted
              itemId: this.itemId,
              dis_val: this.disVal,
              disPriority: this.disPriority, //doubt
              description: this.processContent("string", this.description),
              qty: this.processContent("string", this.qty),
              sku: this.sku,
              mfgDate: this.processContent("string", this.mfgDate),
              expiryDate: this.processContent("string", this.expiryDate),
              shelfLife: this.processContent("string", this.shelfLife),
              brand: this.brand,
              address: this.processContent("string", this.address),
              ean: this.ean,
              active: this.active,
              dropShipAddress: this.processContent(
                "string",
                this.dropShipAddress
              ),
              similarProductId: this.similarProductId,
              maxLimit: this.maxLimit,
              uploadedAt: new Date(),
              updatedAt: new Date()
            })
            .then(res => {
              //// to show
              this.message = "Data Upload Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              console.log(err);

              this.message = "Data Upload In Firebase Failed";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        })
        .catch(err => {
          //// to show
          this.message = "Data Upload Failed";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        });
    },

    uploadImage() {
      if (this.media[0] != null && this.media[0] != undefined) {
        let file = this.$refs.image.files[0];
        this.image = null;
        return firestorage
          .ref("items/" + file.name)
          .put(file)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            console.log(err);

            //// to show
            this.message = "Image Upload Failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);

            return null;
          });
      }
    },

    uploadThumbnail() {
      if (this.media[1] != null && this.media[1] != undefined) {
        let fileT = this.$refs.thumbnail.files[0];
        this.thumbnail = null;
        return firestorage
          .ref("thumbnails/" + fileT.name)
          .put(fileT)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            console.log(err);

            //// to show
            this.message = "Thumbnail Upload failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);

            return null;
          });
      }
    },

    uploadDetailImage(file) {
      if (file) {
        return firestorage
          .ref("items/" + file.name)
          .put(file)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            console.log(err);

            //// to show
            this.message = "More Images Upload failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);

            return null;
          });
      }
    },

    validate() {
      this.errors = [];
      if (
        this.itemId &&
        this.name &&
        this.discount >= 0 &&
        this.currentPrice > 0 &&
        this.unit &&
        this.mrp > 0 &&
        this.msp > 0 &&
        this.stock > 0 &&
        this.allcat &&
        this.disPriority > 0 &&
        this.description &&
        (this.disVal == true || this.disVal == false) &&
        this.qty &&
        this.similarProductId > 0 &&
        this.brand
      ) {
        return true;
      } else {
        if (!this.itemId) {
          Vue.set(this.errors, 0, "Fill valid Item Id");
        }
        if (!this.name) {
          Vue.set(this.errors, 1, "Fill in the name");
        }
        if (!this.unit) {
          Vue.set(this.errors, 2, "Choose Valid Unit");
        }
        if (this.mrp <= 0) {
          Vue.set(this.errors, 3, " MRP can't be 0");
        }

        if (this.currentPrice <= 0) {
          Vue.set(this.errors, 4, "Current Price can't be 0");
        }
        if (this.msp <= 0) {
          Vue.set(this.errors, 5, "MSP can't be 0");
        }
        if (this.stock <= 0) {
          Vue.set(this.errors, 6, "Stock Value can't be 0");
        }
        if (!this.allcat) {
          Vue.set(this.errors, 8, "Fill in the All category");
        }
        if (this.disPriority <= 0) {
          Vue.set(this.errors, 10, "Fill in the Priority");
        }
        if (this.disVal != true && this.disVal != false) {
          Vue.set(this.errors, 9, "Choose valid Discount Value");
        }
        if (!this.description) {
          Vue.set(this.errors, 12, "Fill in the Description");
        }
        if (!this.qty) {
          Vue.set(this.errors, 13, "Quantity can't be 0");
        }

        if (this.qty <= 0) {
          Vue.set(this.errors, 15, "You need to choose one option");
        }
        if (this.brand.length == 0 || this.brand == null) {
          Vue.set(this.errors, 16, "Choose a Brand");
        }

        Vue.set(
          this.errors,
          14,
          "Something is missing!! Check out what you left"
        );
        return false;
      }
    }
  }
};
</script>

<style scoped>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}
</style>



