<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
            <div class="card-body">
              <!-- header -->
              <div class="row mt-4 mb-4">
                <div class="col-6 content-header">
                  <div class="pl-4">
                    <i class="far fa-list-alt"></i>
                    <h5 class="pl-2 d-inline-block"><strong>RETURN REQUESTED ORDERS</strong></h5>
                  </div>
                </div>
                <div class="col-6 content-header small">
                  <span class="float-right">
                  <router-link to="/" class="cursor-pointer" tag="span">
                      dashboard
                  </router-link> >
                  <router-link to="/order/dashboard" class="cursor-pointer" tag="span">
                      sales order
                  </router-link>
                   >
                  <router-link to="/order/return/request" class="cursor-pointer" tag="span">
                      return request
                  </router-link>

                  </span>
                </div>
              </div>
              <hr class="pl-4 pr-4" />
              <!-- content -->
              <DashBoard :newOrders="orders" :disable="disable" :status="status" @fetchOrders="fetchOrders" />

            </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import DashBoard from "./DashBoard.vue";
import axios from "axios";
import Vue from "vue";
import db from "../../firebaseInit";
import firebase from "firebase";
import { firestorage } from "../../firebaseInit";
import { domain } from "@/store/variable";
import { utcHours } from "@/store/variable";
import { utcMins } from "@/store/variable";
import store from "@/store/store";

export default {
  name: "RequestedCancelOrders",
  components: {
    DashBoard
  },

  data() {
    return {
      status: 2,
      orders: [],
      disable: false
    };
  },

  mounted() {
    this.fetchOrders();
  },

  computed: {
    size() {
      return store.state.orderPageSize;
    }
  },

  methods: {
    fetchOrders(page) {
      let url = domain + "order";
      if (!page) {
        page = 1;
      }
      this.disable = true;
      axios
        .get(url, {
          params: {
            status: this.status,
            user: "budhbak",
            products: "budhbak",
            returned: "budhbak",
            payment_mode: "budhbak",
            userAddress: "budhbak",
            order: "updated_at",
            orderWay: "desc",
            page: page,
            limit: this.size
          }
        })
        .then(res => {
          let ordersList = [];
          res.data.data.forEach(doc => {
            let dateTime = new Date(doc.orderTime);
            dateTime.setHours(dateTime.getHours() + utcHours);
            dateTime.setMinutes(dateTime.getMinutes() + utcMins);
             let month = dateTime.getMonth() + 1;
            let date =
              dateTime.getDate() +
              "/" +
              month +
              "/" +
              dateTime.getFullYear();
            var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
            var hours = dateTime.getHours() % 12 || 12;
            let time =
              hours +
              ":" +
              dateTime.getMinutes() +
              ":" +
              dateTime.getSeconds() +
              " " +
              AmOrPm;

            let order = {
              orderId: doc.id,
              products: doc.products,
              orderedItemsCount: doc.orderedItemsCount,
              paymentMode: doc.payment_mode,
              orderDate: date,
              orderTime: time,
              payment: doc.totalPayableAmount,
              paidAmount: doc.paidAmount,
              orderStatus: doc.order_status,
              user: doc.user,
              deliveryAddress: doc.user_address,
              orderBack: doc.returned[0],
              refundId: doc.refundId,
              refund: doc.refund,
              placedFrom: doc.placedFrom,

              checked: false
            };
            ordersList.push(order);

            store.dispatch("patchorderPageCount", res.data.last_page);
            store.dispatch("patchorderPageNumber", res.data.current_page);
            store.dispatch("patchtotalOrders", res.data.total);
          });

          this.orders = ordersList;
        });

      this.disable = false;
    }
  }
};
</script>


<style src="@/assets/css/tableContent.css" scoped>
</style>



