<template>
  <div id="app">
    <Navbar />
    <div class="wrapper">
      <SideBar v-if="user && showSideBar" />
      <div id="content">
        <router-view />
      </div>
      <Setting v-if="overlay == 'overlay active'" />
      <div :class="overlay"></div>
      <!-- fixed button -->
      <div v-if="showAddItem">
        <router-link
          to="/inventory/add"
          class="btn btn-danger btn-sm shadow screen-fixed-button"
        >
          <i class="fas fa-lg fa-cart-plus"></i>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from "./components/Navbar.vue";
import SideBar from "./components/SideBar.vue";
import Setting from "./components/Setting.vue";
import { bus } from "./main";
import store from "./store/store";
import firebase from "firebase";

export default {
  name: "App",
  components: {
    Navbar,
    SideBar,
    Setting
  },

  data() {
    return {
      overlay: "overlay"
    };
  },

  created() {
    bus.$on("switchClass", data => {
      this.switchClass();
    });

    const messaging = firebase.messaging();

    messaging
      .requestPermission()
      .then(() => firebase.messaging().getToken())
      .then(token => {
        console.log(token); // Receiver Token to use in the notification
      })
      .catch(function(err) {
        console.log("Unable to get permission to notify.", err);
      });

    messaging.onMessage(function(payload) {
      console.log("Message received. ", payload);
      // ...
    });
  },

  computed: {
    user() {
      return store.state.user;
    },
    showAddItem() {
      if (
        this.$route.name != "login" &&
        this.$route.name != "Policies" &&
        this.$route.name != "UnAuthorized" &&
        this.$route.name != "invalidUser" &&
        this.$route.name != "underMaintenance"
      ) {
        return true;
      } else {
        return false;
      }
    },

    showSideBar() {
      if (
        this.$route.name != "login" &&
        this.$route.name != "UnAuthorized" &&
        this.$route.name != "invalidUser" &&
        this.$route.name != "underMaintenance"
      ) {
        return true;
      } else {
        return false;
      }
    }
  },

  methods: {
    switchClass() {
      if (this.overlay == "overlay") {
        this.overlay = "overlay active";
      } else {
        this.overlay = "overlay";
      }
    }
  }
};
</script>

<style>
.wrapper {
  display: flex;
  width: 100%;
  align-items: stretch;
}

#content {
  width: 100%;
  margin-left: 260px;
}

.overlay {
  display: none;
  position: fixed;
  /* full screen */
  width: 100vw;
  height: 100vh;
  /* transparent black */
  background: rgba(0, 0, 0, 0.7);
  /* middle layer, i.e. appears below the sidebar */
  z-index: 998;
  opacity: 0;
  /* animate the transition */
  transition: all 0.5s ease-in-out;
}

/* display .overlay when it has the .active class */
.overlay.active {
  display: block;
  opacity: 1;
}

#dismiss {
  width: 35px;
  height: 35px;
  position: absolute;
  /* top right corner of the sidebar */
  top: 10px;
  right: 10px;
}

@media (max-width: 768px) {
  #content {
    margin-left: 0;
  }
}
</style>

<style src="@/assets/css/doorstep.css" scoped></style>



