import axios from "axios";
import Vue from "vue";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import {
  globalMixin
} from '../globalMixin.js';
import {
  inventoryDataMixin
} from './inventoryDataMixin.js';
import $ from "jquery";
import firebase from "firebase";

export const inventoryMixin = {
  mixins: [globalMixin, inventoryDataMixin],
  data() {
    return {
      mrp: 0,
      msp: 0,
      discount: 0,
      currentPrice: 0,

      units: [],
      category: null,
      subcategory: null,
      cocategory: null,
      categories: [],
      subcategories: [],
      cocategories: [],

      availableBrands: [],
      errors: [],
      othersimilarProduct: false,
      similarItems: [],
      media: [], // for making image
      Discalculate: false, // disabling text field to calculate discount value
    }
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  created() {
    this.getBrands();
    this.getUnits();

  },

  methods: {

    addBrand() {
      if (
        (this.user.designation == "admin" ||
          this.user.designation == "webappUploader") &&
        this.user.status == "active"
      ) {
        let url = domain + "item/brand";
        axios
          .post(
            url, {
              name: this.brand
            }, {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            //// to show
            if (res.data) {
              let brand = {
                id: res.data.id,
                name: res.data.name
              };
              this.availableBrands.unshift(brand);

              this.brand = brand;
            }
          })
          .catch(err => {
            // delete in firebase
            /// to show
            this.message = "Uploading Brand Failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    },

    change(type) {
      let files = null;
      if (type == "image") {
        files = this.$refs.image.files;
      } else if (type == "thumbnail") {
        files = this.$refs.thumbnail.files;
      } else if (type == "detailImages") {
        files = this.$refs.detailImages.files;
      }

      if (!files.length) {
        return;
      } else {
        if (type == "image") {
          this.media[0] = null;
          this.createImage(files[0], 0);
        } else if (type == "thumbnail") {
          this.media[1] = null;
          this.createImage(files[0], 1);
        } else if (type == "detailImages") {
          // if (this.media.length >= 2) {
          //   for (var i = 2; i < this.media.length; i++) {
          //     this.media.splice(i,1);
          //   }
          // }

          for (var i = 0; i < files.length; i++) {
            if (this.media.length <= 2) {
              this.createImage(files[i], 2 + i);
            } else {
              this.createImage(files[i], this.media.length + i);
            }
          }
        }
      }
    },

    checkActivationItem(event) {
      if (
        this.subcategories[this.subcategory].active == "1" &&
        this.categories[this.category].active == "1" &&
        this.cocategories[this.cocategory].active == "1"
      ) {
        // do nothing
      } else {
        event.preventDefault();

        // this.active = false;
        if (this.categories[this.category].active != "1") {
          alert("It's category is inactive. So, You can't activate the item.");
        } else if (this.subcategories[this.subcategory].active != "1") {
          alert(
            "It's subcategory is inactive. So, You can't activate the item."
          );
        } else if (this.cocategories[this.cocategory].active != "1") {
          alert(
            "It's cocategory is inactive. So, You can't activate the item."
          );
        }
      }
    },

    checkItem() {
      let url = domain + "item";
      axios
        .get(url, {
          params: {
            name: this.name,
            unit: this.unit,
            qty: this.qty,
            count: "laoDo"
          }
        })
        .then(res => {
          if (res.data) {
            /// to show
            this.message =
              this.name +
              " phele hi daala jaa chuka hai. If you think it's wrong, please contact the management";
            this.success = true;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
            this.duplicate = true;
          } else {
            this.duplicate = false;
          }
        });
      return this.duplicate;
    },

    checkSimilarName() {
      this.searchingItem = true;
      let url = domain + "search";
      this.similarItems = [];
      axios
        .get(url, {
          params: {
            table: "items",
            data: this.processContent("string", this.name)
          }
        })
        .then(res => {
          if (res.data.exactSearch != "" && res.data.exactSearch != null) {
            /// to show
            res.data.exactSearch.forEach(doc => {
              this.similarItems.push(doc);
            });
          } else {
            this.similarItems.push({
              name: "No item Available"
            });

            this.setSimilarId("no", "item");
          }

          this.searchingItem = false;
        });
    },

    createImage(file, index) {
      let reader = new FileReader();
      reader.onload = e => {
        Vue.set(this.media, index, e.target.result);
        if (index == 0) {
          this.image = null;
        } else if (index == 1) {
          this.thumbnail = null;
        } else {
          this.detailImages = [];
        }
      };
      reader.readAsDataURL(file);
    },
    discountCal() {
      if (
        this.mrp != null &&
        this.currentPrice != null &&
        this.mrp != "" &&
        this.currentPrice != "" &&
        this.currentPrice <= this.mrp
      ) {
        this.discount = (
          ((this.mrp - this.currentPrice) / this.mrp) *
          100
        ).toFixed(0);
      } else {
        this.discount = 0;
      }
    },

    generateSKU() {
      //let d = new Date().getTime();
      // let id = "xxxxxxxx".replace(/[x]/g, function(c) {
      //   let r = (d + Math.random() * 8) % 8 | 0;
      //   d = Math.floor(d / 16);
      //   return (c === "x" ? r : (r & 0x1) | 0x5).toString(8);
      // });

      let nameCode = this.name.split(" ");
      let nameId = "";
      nameCode.forEach(word => {
        var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = initialNameLetter.charCodeAt(0);
        if (!(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          nameId = nameId + initialNameLetter;
        }
      });

      let cat = this.categories[this.category].name
        .substring(0, 3)
        .toUpperCase()
        .split("");
      let catId = "";

      cat.forEach(word => {
        //var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = word.charCodeAt(0);
        if (!(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          catId = catId + word;
        }
      });

      let subcat = this.subcategories[this.subcategory].name
        .substring(0, 3)
        .toUpperCase()
        .split("");
      let subcatId = "";

      subcat.forEach(word => {
        //var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = word.charCodeAt(0);
        if (!(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          subcatId = subcatId + word;
        }
      });

      let cocat = this.cocategories[this.cocategory].name
        .substring(0, 3)
        .toUpperCase()
        .split("");
      let cocatId = "";

      cocat.forEach(word => {
        //var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = word.charCodeAt(0);
        if (!(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          cocatId = cocatId + word;
        }
      });

      let id = catId + "-" + subcatId + "-" + cocatId; //"-" + nameId

      return id;
    },

    getBrands() {
      let url = domain + "item/brand";
      let flag = 0;
      axios.get(url).then(res => {
        res.data.forEach((doc, index) => {
          let brand = {
            id: doc.id,
            name: doc.name
          };
          this.availableBrands.push(brand);
        });
      });
    },

    getCoCategories() {
      this.ModalContentLoading = true;
      this.cocategories = [];
      let url = domain + "cocat";

      if (this.subcategories[this.subcategory].active == "0") {
        this.active = false;
      }
      axios
        .get(url, {
          params: {
            coid: this.subcategories[this.subcategory].coid
          }
        })
        .then(res => {
          if (res.data) {
            res.data.forEach(doc => {
              const cocat = {
                name: doc.name,
                itemId: doc.item_id,
                priceBandValue: doc.priceBandValue
              };
              this.cocategories.push(cocat);
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.ModalContentLoading = false;
    },

    getSubCategories() {
      this.ModalContentLoading = true;
      this.subcategories = [];
      this.cocategories = [];
      let url = domain + "subcat";

      if (this.categories[this.category].active == "0") {
        this.active = false;
      }

      axios
        .get(url, {
          params: {
            subcatid: this.categories[this.category].subcatid
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            const subcat = {
              name: doc.name,
              coid: doc.coid,
              active: doc.active
            };
            this.subcategories.push(subcat);
          });
        })
        .catch(err => {
          console.log(err);
        });
      this.ModalContentLoading = false;
    },

    getUnits() {
      this.units = [];
      let url = domain + "item/units";
      axios
        .get(url)
        .then(res => {
          if (res.data) {
            res.data.forEach(doc => {
              this.units.push(doc);
            });
          }
        })
        .catch(err => {
          this.units = [];
          console.log(err);
        });
    },

    fetchCategories() {
      let url = domain + "cat";
      let categories = [];
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            const cat = {
              name: doc.name,
              subcatid: doc.subcatcode,
              active: doc.active
            };
            categories.push(cat);
          });
          this.categories = categories;
        })
        .catch(err => {
          console.log(err);
        });
    },

    processContent(type, data) {
      if (data) {
        if (type == "string") {
          return data.trim();
        } else if (type == "html") {
          let lastIndex = data.lastIndexOf("<p>&nbsp;</p>");
          if (lastIndex != -1 && data.length - 14 == lastIndex) {
            // will remove everywhere
            // data = data.replace(
            //   new RegExp(this.escapeRegExp("<p>&nbsp;</p>"), "g"),
            //   ""
            // );
            data = data.substring(0, lastIndex);
          }
          return data;
        }
      }
      return data;
    },

    profitLossCal() {
      if (
        this.currentPrice != null &&
        this.msp != null &&
        this.currentPrice != "" &&
        this.msp != ""
      ) {
        this.profitLoss = this.currentPrice - this.msp;
        this.profitLossPer = ((this.profitLoss / this.msp) * 100).toFixed(0);
        if (this.profitLoss < 0) {
          this.profitLossPer = Math.abs(this.profitLossPer);
          this.profitLoss = Math.abs(this.profitLoss);
          $("#profitLossModal").modal("show");
        }
      }
    },
    pricaBandCalculator(change) {
      //calculating price band
      if (change == "priceBand" && this.mrp != null && this.mrp != "") {
        //calculating current price
        let takeout = Math.round(this.mrp * (this.priceBand / 100));
        this.currentPrice = this.mrp - takeout;

        //calculating msp
        let ctakeout = Math.round(this.currentPrice * (this.priceBand / 100));
        this.msp = this.currentPrice - ctakeout;
      } else if (change == "mrp" && this.priceBand != null) {
        // this.priceBand = (
        //   ((this.mrp - this.currentPrice) / this.mrp) *
        //   100
        // ).toFixed(0);

        let takeout = Math.round(this.mrp * (this.priceBand / 100));
        this.currentPrice = this.mrp - takeout;

        //calculating msp
        let ctakeout = Math.round(this.currentPrice * (this.priceBand / 100));
        this.msp = this.currentPrice - ctakeout;
      } else if (change == "currentPrice" && this.priceBand != null) {
        // this.priceBand = (
        //   ((this.mrp - this.currentPrice) / this.mrp) *
        //   100
        // ).toFixed(0);

        //calculating msp
        let ctakeout = Math.round(this.currentPrice * (this.priceBand / 100));
        this.msp = this.currentPrice - ctakeout;
      } else {
        //ghanta
      }

      this.discountCal();
    },

    removeImage(type, index) {
      if (type == "image") {
        if (!isNaN(index)) {
          Vue.set(this.media, 0, undefined);
        } else {
          this.image = null;
        }
      } else if (type == "thumbnail") {
        if (!isNaN(index)) {
          Vue.set(this.media, 1, undefined);
        } else {
          this.thumbnail = null;
        }
      } else {
        //detail images start at pos 2 so that's why add 2
        if (!isNaN(index)) {
          if (this.detailImages.length > 0) {
            this.detailImages.splice(index, 1);
          } else {
            this.media.splice(index + 2, 1);
          }
        } else {
          this.detailImages.splice(index + 2, 1);
          this.detailImages = [];
        }
      }
    },

    searchItem() {
      if (this.searchProductID) {
        let url = domain + "item";
        this.disable = true;
        this.disableMessage = "Checking our Databases...";
        axios
          .get(url, {
            params: {
              productId: this.searchProductID.trim()
            }
          })
          .then(res => {
            if (res.data.length > 0) {
              res.data.forEach(doc => {
                this.productId = doc.productId;
                this.itemId = doc.itemId;
                this.currentPrice = doc.currentPrice;
                this.name = doc.name;
                this.image = doc.image;
                this.thumbnail = doc.thumbnail;
                this.detailImages = doc.detailImages;
                this.discount = doc.discount;
                this.unit = doc.unit;
                this.unit2 = doc.unit2;
                this.color = doc.color;
                this.mrp = doc.MRP;
                this.stock = doc.stock;
                this.home = doc.home; // doubt;
                this.allcat = doc.allcat; //doubt;
                this.itemId = doc.itemId;
                this.sku = doc.sku;

                if (doc.dis_val == 1) {
                  this.disVal = true;
                } else {
                  this.disVal = false;
                }

                this.disPriority = doc.disPriority; //dou;
                this.description = doc.description;
                this.ingredients = doc.ingredients;
                this.features = doc.features;
                this.msp = doc.msp;
                this.qty = doc.qty;
                this.mfgDate = doc.mfgDate;
                this.expiryDate = doc.expiryDate;
                this.shelfLife = doc.shelfLife;
                this.brand = doc.item_brand;
                this.address = doc.address;
                this.ean = doc.EAN;
                this.similarProductId = doc.similarProductId;
                this.uploadedBy = doc.uploadedBy;
                this.uploadedAt = doc.uploadedAt;
                this.updatedAt = doc.updatedAt;
                this.maxLimit = doc.maxLimit;
                this.priceBand = Math.round(
                  ((this.currentPrice - this.msp) / this.currentPrice) * 100
                );

                if (doc.dropShipAddress) {
                  this.dropShip = true;
                  this.dropShipAddress = doc.dropShipAddress;
                }
                if (doc.active == 1) {
                  this.active = true;
                } else {
                  this.active = false;
                }

                //

                // fetching category cocat subcat
                let courl = domain + "cocat";
                axios
                  .get(courl, {
                    params: {
                      item_id: this.itemId
                    }
                  })
                  .then(res => {
                    if (res.data) {
                      this.cocategories = res.data;
                      // pusing just 1 cocategory to array and setting it's index
                      this.cocategory = 0;
                      this.priceBand = this.cocategories[
                        this.cocategory
                      ].priceBandValue;
                      //subcat
                      let suburl = domain + "subcat";
                      axios
                        .get(suburl, {
                          params: {
                            coid: res.data[0].coid
                          }
                        })
                        .then(ressub => {
                          if (ressub.data) {
                            this.subcategories = ressub.data;
                            this.subcategory = 0;
                            // fetching category
                            let caturl = domain + "cat";
                            axios
                              .get(caturl, {
                                params: {
                                  subcatcode: ressub.data[0].subcatid
                                }
                              })
                              .then(rescat => {
                                if (rescat.data) {
                                  this.categories = rescat.data;
                                  this.category = 0;
                                }
                              });
                          }
                        });
                    }
                  });
              });
              this.disable = false;
              this.disableMessage =
                "Uploading Data, Kripya Shanti banaye rkhe...";
            } else {
              this.message = "This product Don't exist in database";
              this.success = true;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);

              this.disable = false;
              this.disableMessage =
                "Uploading Data, Kripya Shanti banaye rkhe...";
            }
          });
      } else {
        if (
          this.itemId &&
          this.name &&
          this.discount >= 0 &&
          this.currentPrice > 0 &&
          this.unit &&
          this.mrp > 0 &&
          this.msp > 0 &&
          this.stock > 0 &&
          this.allcat &&
          this.disPriority > 0 &&
          this.description &&
          (this.disVal == true || this.disVal == false) &&
          this.qty > 0
        ) {
          // agr koi data hai tho gayb na kre, dimag ka bhosda ho jaega agr delete hua itna mehnat se likha data//

          this.category = null;
          this.subcategory = null;
          this.cocategory = null;
          this.categories = [];
          this.subcategories = [];
          this.cocategories = [];
          this.fetchCategories();
        } else {
          ///////otherwise udha do sb///////
          this.currentPrice = 0;
          this.name = null;
          this.image = null;
          this.thumbnail = null;
          this.discount = 0;
          this.unit = null;
          this.unit2 = null;
          this.mrp = 0;
          this.msp = 0;
          this.stock = 0;
          this.home = "hcat"; // doubted
          this.allcat = "acat"; //doubted
          this.itemId = null;
          this.disPriority = 0; //doubt
          this.qty = 0;
          this.sku = null;
          this.description = null;
          this.media = [];
          this.mfgDate = null;
          this.expiryDate = null;
          this.shelfLife = null;
          this.brand = null;
          this.address = null;
          this.ean = 0;
          this.disVal = false;
          this.dropShip = false;
          this.dropShipAddress = null;
          this.maxLimit = 0;
          this.ingredients = "<p></p>";
          this.packingType = null;
          this.features = "<p></p>";
          this.category = null;
          this.subcategory = null;
          this.cocategory = null;
          this.categories = [];
          this.subcategories = [];
          this.cocategories = [];
          this.fetchCategories();
        }
      }
    },

    setBrand(brand) {
      this.enterBrand = false;
      this.brand = brand;
    },

    setCocategory() {
      if (this.cocategories[this.cocategory].active == "0") {
        this.active = false;
      }
      this.itemId = this.cocategories[this.cocategory].itemId;
      this.priceBand = this.cocategories[this.cocategory].priceBandValue;
    },

    setCurrentPrice() {
      this.discountCal();
      this.pricaBandCalculator("currentPrice");
    },

    setMrp() {
      this.discountCal();
      this.pricaBandCalculator("mrp");
    },

    setSimilarId(type, data) {
      if (type) {
        if (type == "id") {
          this.similarProductId = this.similarItems[data].similarProductId;
        } else if (type == "productId") {
          let url = domain + "item";
          this.searchingItem = true;

          let id = data.target.value;
          axios
            .get(url, {
              params: {
                productId: id
              }
            })
            .then(res => {
              res.data.forEach(doc => {
                this.similarProductId = doc.similarProductId;
              });
              this.searchingItem = false;
            });
        } else {
          let url = domain + "item";
          this.searchingItem = true;
          axios
            .get(url, {
              params: {
                max: "similarProductId"
              }
            })
            .then(res => {
              this.similarProductId = Number(res.data) + 1;
              this.searchingItem = false;
            });
        }
      }
    },

    validate() {
      this.errors = [];
      if (
        this.itemId &&
        this.name &&
        this.discount >= 0 &&
        this.currentPrice > 0 &&
        this.unit &&
        this.mrp > 0 &&
        this.msp > 0 &&
        this.stock > 0 &&
        this.allcat &&
        this.disPriority > 0 &&
        this.description &&
        (this.disVal == true || this.disVal == false) &&
        this.qty &&
        this.similarProductId > 0 &&
        this.brand.length == undefined
      ) {
        return true;
      } else {
        if (!this.itemId) {
          Vue.set(this.errors, 0, "Fill valid Item Id");
        }
        if (!this.name) {
          Vue.set(this.errors, 1, "Fill in the name");
        }
        if (!this.unit) {
          Vue.set(this.errors, 2, "Choose Valid Unit");
        }
        if (this.mrp <= 0) {
          Vue.set(this.errors, 3, " MRP can't be 0");
        }

        if (this.currentPrice <= 0) {
          Vue.set(this.errors, 4, "Current Price can't be 0");
        }
        if (this.msp <= 0) {
          Vue.set(this.errors, 5, "MSP can't be 0");
        }
        if (this.stock <= 0) {
          Vue.set(this.errors, 6, "Stock Value can't be 0");
        }
        if (!this.allcat) {
          Vue.set(this.errors, 8, "Fill in the All category");
        }
        if (this.disPriority <= 0) {
          Vue.set(this.errors, 10, "Fill in the Priority");
        }
        if (this.disVal != true && this.disVal != false) {
          Vue.set(this.errors, 9, "Choose valid Discount Value");
        }
        if (!this.description) {
          Vue.set(this.errors, 12, "Fill in the Description");
        }
        if (!this.qty) {
          Vue.set(this.errors, 13, "Quantity can't be 0");
        }

        if (this.qty <= 0) {
          Vue.set(this.errors, 15, "You need to choose one option");
        }
        if (this.brand.length == 0) {
          Vue.set(this.errors, 16, "Choose a Brand");
        }

        Vue.set(
          this.errors,
          14,
          "Something is missing!! Check out what you left"
        );
        return false;
      }
    }
  },
}



