                      <small>(When this item was added)</small>
                    </label>
                    <input
                      type="text"
                      v-model="uploadedAt"
                      disabled
                      class="form-control form-control-sm"
                    />
                  </div>

                  <div class="col-sm-6">
                    <label class="active" for="all category">
                      Updated At
                      <small>(When item was last updated)</small>
                    </label>
                    <input
                      type="text"
                      v-model="updatedAt"
                      disabled
                      class="form-control form-control-sm"
                    />
                  </div>
                </div>

                <!-- buttons -->
                <div class="form-row">
                  <div class="col text-center">
                    <button
                      class="btn btn-sm btn-success font-weight-bold d-inline"
                      type="submit"
                      @click.prevent="edit()"
                      :disabled="disable"
                    >
                      <i class="fas fa-save pr-1"></i>
                      UPDATE
                    </button>
                    <router-link
                      to="/inventory"
                      class="btn btn-sm btn-secondary"
                    >
                      <i class="fas fa-ban pr-1"></i>
                      <div class="font-weight-bold d-inline">CANCEL</div>
                    </router-link>
                  </div>
                </div>

                <!-- general error message -->
                <div class="form-row" v-if="errors[14]">
                  <div class="col text-center text-danger">
                    {{ errors[14] }}
                  </div>
                </div>
              </form>
            </div>
          </div>
          <!-- modal -->
          <div class="modal" tabindex="-1" role="dialog" id="profitLossModal">
            <div class="modal-dialog modal-dialog-centered" role="document">
              <div class="modal-content">
                <div class="modal-header">
                  <h6 class="modal-title">
                    <i class="fas fa-exclamation-circle text-danger"></i>
                    <div class="d-inline font-weight-bold pl-2 text-danger">
                      Alert! Proceed with precaution
                    </div>
                  </h6>
                  <button
                    type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close"
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div class="modal-body">
                  <strong style="opacity:0.75">
                    Are you sure you wanna upload
                    <span class="font-weight-bold text-success"
                      > {{ currentPrice }}</span
                    >
                    as current price ?
                  </strong>
                  <p class="mt-3">
                    This will incur a
                    <span class="text-danger font-weight-bold">
                      loss of 
                      {{ this.profitLoss }}
                    </span>
                    that is
                    <span class="font-weight-bold text-danger"
                      >{{ profitLossPer }}%</span
                    >
                    to doorstep
                  </p>
                </div>
                <div class="modal-footer">
                  <div class="mr-auto">
                    <a href="#" class="font-weight-bold text-dark">
                      <i class="fas fa-question pr-1"></i>Get Help
                    </a>
                  </div>
                  <button
                    type="button"
                    class="btn btn-secondary btn-sm font-weight-bold"
                    data-dismiss="modal"
                  >
                    <i class="fas fa-ban pr-2" @click="this.mrp = 0"></i>CANCEL
                  </button>
                  <button
                    type="button"
                    class="btn btn-danger btn-sm font-weight-bold"
                    data-dismiss="modal"
                  >
                    <i class="fas fa-cloud-upload-alt pr-1 font-weight-bold"></i
                    >UPLOAD
                  </button>
                </div>
              </div>
            </div>
          </div>
          <!-- modal end -->
        </div>
        <div class="col-lg-2"></div>
      </div>
      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Vue from "vue";
import db from "../firebaseInit";
import { firestorage } from "../firebaseInit";
import firebase from "firebase";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import Loader from "../Loader";
import LoaderGif from "../LoaderGif";
import axios from "axios";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith,
  fromHeader
} from "@/store/variable";
import store from "@/store/store";

import { inventoryMixin } from "../Mixins/Inventory/inventoryMixin.js";

export default {
  name: "editItem",
  mixins: [inventoryMixin],
  components: {
    Loader,
    LoaderGif
  },

  data() {
    return {
      editor: ClassicEditor,
      editorData: [],
      editorConfig: {
        // The configuration of the editor.
      },
      ModalContentLoading: false,

      // item details
      active: true,
      productId: null,
      currentPrice: 0,
      color: null,
      name: null,
      image: null,
      thumbnail: null,
      detailImages: [],
      discount: 0,
      unit: null,
      unit2: null,
      mrp: 0,
      msp: 0,
      stock: 0,
      home: "hcat", // doubted
      allcat: "acat", //doubted
      itemId: null,
      disPriority: 0, //doubt
      description: "<p></p>",
      qty: 0,
      sku: null,
      mfgDate: null,
      expiryDate: null,
      shelfLife: null,
      brand: null,
      address: null,
      ean: 0,
      barcode: null,
      uploadedBy: null,
      uploadedAt: "",
      updatedAt: "",
      profitLoss: 0,
      profitLossPer: 0,
      dropShip: false,
      dropShipAddress: null,
      maxLimit: 0,
      ingredients: "<p></p>",
      packingType: null,
      features: "<p></p>",
      similarProductId: 0,
      imageOption: true,
      thumbnailOption: true,
      detailImageOption: true,
      priceBand: 0,
      disVal: false,
      message: null,
      disable: false,
      success: false,

      searchingItem: false,

      itemHeading: null,
      enterBrand: false,
      originalItem: []
    };
  },

  created() {
    let url = domain + "cat";
    axios
      .get(url)
      .then(res => {
        res.data.forEach(doc => {
          const cat = {
            name: doc.name,
            subcatid: doc.subcatcode
          };
          this.categories.push(cat);
        });
      })
      .catch(err => {
        console.log(err);
      });
  },

  beforeRouteEnter(to, from, next) {
    let url = domain + "item";
    axios
      .get(url, {
        params: {
          productId: to.params.productId
        },
        headers: {
          from: fromHeader
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          next(vm => {
            vm.productId = doc.productId;
            vm.itemId = doc.itemId;
            vm.currentPrice = doc.currentPrice;
            vm.name = doc.name;
            vm.image = doc.image;
            vm.thumbnail = doc.thumbnail;
            // vm.detailImages = doc.detailImages;

            doc.detailImages.forEach(image => {
              vm.detailImages.push(image);
            });

            vm.discount = doc.discount;
            vm.unit = doc.unit;
            vm.unit2 = doc.unit2;
            vm.color = doc.color;
            vm.mrp = doc.MRP;
            vm.barcode = doc.barcode;
            vm.stock = doc.stock;
            vm.home = doc.home; // doubt
            vm.allcat = doc.allcat; //doubt
            vm.itemId = doc.itemId;
            vm.sku = doc.sku;
            vm.similarProductId = doc.similarProductId;

            if (doc.dis_val == 1) {
              vm.disVal = true;
            } else {
              vm.disVal = false;
            }

            vm.disPriority = doc.disPriority; //dou
            vm.description = doc.description;
            vm.features = doc.features;
            vm.ingredients = doc.ingredients;
            vm.msp = doc.msp;
            vm.qty = doc.qty;
            vm.mfgDate = doc.mfgDate;
            vm.expiryDate = doc.expiryDate;
            vm.shelfLife = doc.shelfLife;
            vm.brand = doc.item_brand;
            vm.address = doc.address;
            vm.ean = doc.EAN;
            vm.maxLimit = doc.maxLimit;

            vm.priceBand = Math.round(
              ((doc.currentPrice - doc.msp) / doc.currentPrice) * 100
            );

            if (doc.dropShipAddress) {
              vm.dropShip = true;
              vm.dropShipAddress = doc.dropShipAddress;
            }
            if (doc.active == 1) {
              vm.active = true;
            } else {
              vm.active = false;
            }
            vm.uploadedBy = doc.uploadedBy;
            vm.uploadedAt = doc.uploadedAt;
            vm.updatedAt = doc.updatedAt;

            vm.originalItem = doc;
            vm.itemHeading = doc.name;
          });
        });
      });
  },

  beforeRouteLeave(to, from, next) {
    if (
      this.name != this.originalItem["name"] ||
      this.discount != this.originalItem["discount"] ||
      this.mrp != this.originalItem["MRP"] ||
      this.msp != this.originalItem["msp"] ||
      this.stock != this.originalItem["stock"] ||
      this.allcat != this.originalItem["allcat"] ||
      this.disPriority != this.originalItem["disPriority"] ||
      this.description != this.originalItem["description"] ||
      this.qty != this.originalItem["qty"] ||
      this.mfgDate != this.originalItem["mfgDate"] ||
      this.expiryDate != this.originalItem["expiryDate"] ||
      this.shelfLife != this.originalItem["shelfLife"] ||
      this.brand != this.originalItem["brand"] ||
      this.address != this.originalItem["address"] ||
      this.ean != this.originalItem["ean"]
    ) {
      if (
        confirm("Your changes will be lost. Do you want to leave the page?")
      ) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },

  watch: {
    $route: "fetchItem"
  },

  methods: {
    async edit() {
      // binging preloader on
      //this.duplicate = await this.checkItem();

      // binging preloader on
      // check if image is in use by others
      // if (this.image) {
      //   let check = 0;
      //   db.collection("items")
      //     .where("image", "==", this.image)
      //     .get()
      //     .then(resInner => {
      //       resInner.forEach(qrySnapshot => {
      //         if (qrySnapshot.data().productId != this.productId) {
      //           check = 1;
      //         }
      //       });
      //       if (check == 0) {
      //         firestorage.refFromURL(this.image).delete();
      //       }
      //     });
      // }

      // if (this.thumbnail) {
      //   let check = 0;
      //   db.collection("items")
      //     .where("thumbnail", "==", this.thumbnail)
      //     .get()
      //     .then(resInner => {
      //       resInner.forEach(qrySnapshot => {
      //         if (qrySnapshot.data().productId != this.productId) {
      //           check = 1;
      //         }
      //       });
      //       if (check == 0) {
      //         firestorage.refFromURL(this.thumbnail).delete();
      //       }
      //     });
      // }

      let flag = true;

      if (
        this.validate() &&
        //this.duplicate == false &&
        (this.user.designation == "admin" ||
          this.user.designation == "webappUploader") &&
        this.user.status == "active"
      ) {
        this.disable = true;
        this.disableMessage = "Uploading Data, Kripya Shanti banaye rkhe...";

        // add brand
        if (this.enterBrand) {
          await this.addBrand();
          if (!isNaN(this.brand)) {
            flag = false;
          }
        }

        if (
          this.media[0] &&
          this.media[0] != null &&
          this.media[0] != undefined &&
          flag == true
        ) {
          if (
            (this.user.designation == "admin" ||
              (this.user.designation == "webappUploader" &&
                this.user.package == 3)) &&
            this.user.status == "active"
          ) {
            await this.uploadImage().then(downloadURL => {
              this.image = downloadURL;
            });
          } else {
            if (this.user.package != 3) {
              alert("Your package does not allow you to upload image");
            } else if (this.user.status != "active") {
              alert("Aap se yeh adikar cheen liya gya hai");
            }
            flag = false;
            this.disable = false;

            return;
          }
        }

        if (
          this.media[1] &&
          this.media[1] != null &&
          this.media[1] != undefined &&
          flag == true
        ) {
          if (
            (this.user.designation == "admin" ||
              (this.user.designation == "webappUploader" &&
                this.user.package == 3)) &&
            this.user.status == "active"
          ) {
            await this.uploadThumbnail().then(downloadURL => {
              this.thumbnail = downloadURL;
            });
          } else {
            if (this.user.package != 3) {
              alert("Your package does not allow you to upload image");
            } else if (this.user.status != "active") {
              alert("Aap se yeh adikar cheen liya gya hai");
            }
            flag = false;
            this.disable = false;

            return;
          }
        }

        if (
          this.media[2] &&
          this.media[2] != null &&
          this.media[2] != undefined &&
          flag == true
        ) {
          if (
            (this.user.designation == "admin" ||
              (this.user.designation == "webappUploader" &&
                this.user.package == 3)) &&
            this.user.status == "active" &&
            this.image != null &&
            this.image != ""
          ) {
            let files = this.$refs.detailImages.files;
            let mediaURL = [];
            this.detailImages = [];
            for (var i = 0; i < files.length; i++) {
              let file = files[i];

              await this.uploadDetailImage(file).then(downloadURL => {
                this.detailImages.push(downloadURL);
              });
            }
          } else {
            if (this.user.package != 3 && this.user.designation != "admin") {
              alert("Your package does not allow you to upload image");
            } else if (this.user.status != "active") {
              alert("Aap se yeh adikar cheen liya gya hai");
            } else if (this.image == null || this.image == "") {
              alert("You need to upload the main image first");
            }
            flag = false;
            this.disable = false;
            return;
          }
        }

        if (flag == true) {
          if (
            (this.image && this.user.package == 3) ||
            this.user.package == 1 ||
            this.user.designation == "admin"
          ) {
            this.updateData();
          } else {
            if (this.image && this.user.package != 3) {
              alert("Your package does not allow you to upload image");
            } else if (!this.image && this.user.package == 3) {
              alert("Your package demands to upload image with it");
            }
            this.disable = false;
          }
        }
      }
    },

    fetchItem() {
      let url = domain + "item";
      axios
        .get(url, {
          params: {
            productId: this.$route.params.productId
          },
          headers: {
            from: fromHeader
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            this.productId = doc.productId;
            this.itemId = doc.itemId;
            this.currentPrice = doc.currentPrice;
            this.name = doc.name;
            this.image = doc.image;
            this.thumbnail = doc.thumbnail;
            doc.detailImages.forEach(image => {
              this.detailImages.push(image);
            });

            this.discount = doc.discount;
            this.unit = doc.unit;
            this.unit2 = doc.unit2;
            this.color = doc.color;
            this.mrp = doc.MRP;
            this.stock = doc.stock;
            this.home = doc.home; // doubt;
            this.allcat = doc.allcat; //doubt;
            this.itemId = doc.itemId;
            this.sku = doc.sku;
            this.barcode = doc.barcode;
            this.similarProductId = doc.similarProductId;
            if (doc.dis_val == 1) {
              this.disVal = true;
            } else {
              this.disVal = false;
            }

            this.disPriority = doc.disPriority; //dou;
            this.description = doc.description;
            this.ingredients = doc.ingredients;
            this.features = doc.features;
            this.msp = doc.msp;
            this.qty = doc.qty;
            this.mfgDate = doc.mfgDate;
            this.expiryDate = doc.expiryDate;
            this.shelfLife = doc.shelfLife;
            this.brand = doc.item_brand;
            this.address = doc.address;
            this.ean = doc.EAN;
            this.uploadedBy = doc.uploadedBy;
            this.uploadedAt = doc.uploadedAt;
            this.updatedAt = doc.updatedAt;
            this.maxLimit = doc.maxLimit;
            this.priceBand = Math.round(
              ((this.currentPrice - this.msp) / this.currentPrice) * 100
            );

            if (doc.dropShipAddress) {
              this.dropShip = true;
              this.dropShipAddress = doc.dropShipAddress;
            }
            if (doc.active == 1) {
              this.active = true;
            } else {
              this.active = false;
            }

            // imporoving data
            this.originalItem = doc;
            this.itemHeading = doc.name;
          });
        });
    },

    ////////////////////////////////////
    updateData() {
      if (
        this.category &&
        this.category >= 0 &&
        this.subcategory &&
        this.subcategory >= 0 &&
        this.cocategory &&
        this.cocategory >= 0
      ) {
        this.sku = this.generateSKU();
      }

      this.name = this.name.toLowerCase().replace(/(\s+.)|^\w/gi, function(v) {
        return v.toUpperCase();
      });
      if (!this.dropShip) {
        this.dropshipAddress = null;
      }
      if (!this.mfgDate) {
        this.mfgDate = null;
      }
      if (!this.expiryDate) {
        this.expiryDate = null;
      }

      let url = domain + "item/" + this.productId;

      axios
        .post(
          url,
          {
            _method: "put",
            currentPrice: this.currentPrice,
            name: this.processContent("string", this.name),
            image: this.image,
            detailImages: this.detailImages,
            thumbnail: this.thumbnail,
            discount: this.discount,
            ingredients: this.processContent("html", this.ingredients),
            features: this.processContent("html", this.features),
            packingType: this.packingType,
            unit: this.unit,
            unit2: this.unit2,
            color: this.processContent("string", this.color),
            MRP: this.mrp,
            msp: this.msp,
            stock: this.stock,
            home: this.home, // doubted
            allcat: this.allcat, //doubted
            itemId: this.itemId,
            dis_val: this.disVal,
            disPriority: this.disPriority, //doubt
            description: this.processContent("string", this.description),
            qty: this.processContent("string", this.qty),
            sku: this.sku,
            mfgDate: this.processContent("string", this.mfgDate),
            expiryDate: this.processContent("string", this.expiryDate),
            shelfLife: this.processContent("string", this.shelfLife),
            brand: this.brand.name,
            brandName: this.brand.id,
            address: this.processContent("string", this.address),
            ean: this.ean,
            barcode: this.barcode,
            active: this.active,
            dropShipAddress: this.processContent(
              "string",
              this.dropShipAddress
            ),
            similarProductId: this.similarProductId,
            maxLimit: this.maxLimit
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          this.updatedAt = new Date();

          this.originalItem = res.data;

          db.collection("items")
            .where("productId", "==", this.productId)
            .get()
            .then(res => {
              res.forEach(doc => {
                doc.ref.update({
                  currentPrice: this.currentPrice,
                  name: this.processContent("string", this.name),
                  image: this.image,
                  detailImages: this.detailImages,
                  thumbnail: this.thumbnail,
                  discount: this.discount,
                  ingredients: this.processContent("html", this.ingredients),
                  features: this.processContent("html", this.features),
                  packingType: this.packingType,
                  unit: this.unit,
                  unit2: this.unit2,
                  color: this.processContent("string", this.color),
                  MRP: this.mrp,
                  msp: this.msp,
                  stock: this.stock,
                  home: this.home, // doubted
                  allcat: this.allcat, //doubted
                  itemId: this.itemId,
                  dis_val: this.disVal,
                  disPriority: this.disPriority, //doubt
                  description: this.processContent("string", this.description),
                  qty: this.processContent("string", this.qty),
                  sku: this.sku,
                  mfgDate: this.processContent("string", this.mfgDate),
                  expiryDate: this.processContent("string", this.expiryDate),
                  shelfLife: this.processContent("string", this.shelfLife),
                  brand: this.brand,
                  address: this.processContent("string", this.address),
                  ean: this.ean,
                  barcode: this.barcode,
                  active: this.active,
                  dropShipAddress: this.processContent(
                    "string",
                    this.dropShipAddress
                  ),
                  similarProductId: this.similarProductId,
                  maxLimit: this.maxLimit,
                  updatedAt: new Date()
                });
              });
            })
            .then(res => {
              /////
              /////
              //// to show
              this.message = "Data Update Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              console.log(err);
              //// to show
              this.message = "Some Error Occured in firebase!!";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        })
        .catch(err => {
          /// to show
          this.message = "Data Upload In MYSql Failed";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
          //});
        });
    },

    uploadImage() {
      if (this.media[0] != null && this.media[0] != undefined) {
        let file = this.$refs.image.files[0];
        this.image = null;
        return firestorage
          .ref("items/" + file.name)
          .put(file)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            console.log(err);

            //// to show
            this.message = "Image Upload Failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);

            return null;
          });
      }
    },

    uploadThumbnail() {
      if (this.media[1] != null && this.media[1] != undefined) {
        let fileT = this.$refs.thumbnail.files[0];
        this.thumbnail = null;
        return firestorage
          .ref("thumbnails/" + fileT.name)
          .put(fileT)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            console.log(err);

            //// to show
            this.message = "Thumbnail Upload failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);

            return null;
          });
      }
    },

    uploadDetailImage(file) {
      if (file) {
        return firestorage
          .ref("items/" + file.name)
          .put(file)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            console.log(err);

            //// to show
            this.message = "More Images Upload failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);

            return null;
          });
      }
    }
  }
};
</script>

<style>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.modal-dialog {
  overflow-y: initial !important;
}
.modal-body {
  overflow-y: auto;
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.gmd-3 {
  -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19),
    0 6px 6px rgba(0, 0, 0, 0.23);
  -moz-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19),
    0 6px 6px rgba(0, 0, 0, 0.23);
  -ms-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  -o-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}
.error {
  font-size: 14px;
}
</style>



