            </th>
          </tr>
        </thead>
        <tbody v-if="items.length > 0 && user" class="table-hover">
          <tr
            v-for="(item, index) in items"
            :key="index"
            @mouseenter="showHideImage(index)"
            @mouseleave="showHideImage(index)"
            :class="getRowClass(index)"
          >
            <td
              style="font-size:12px;"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'name' &&
                    editItemProductId == item.productId
                "
              />
              <span
                @dblclick="editProperty(item.productId, 'name', item.name)"
                v-else
              >
                <i
                  class="fas fa-circle text-success"
                  aria-hidden="true"
                  v-if="item.active == 1"
                ></i>
                <i
                  class="fas fa-circle text-danger"
                  aria-hidden="true"
                  v-else
                ></i>
                {{ item.name }}
              </span>
            </td>
            <td
