                @click="copyProductId(item.productId)"
              ></i>
              <input
                type="hidden"
                :value="item.productId"
                :id="item.productId"
              />
            </td>
            <td
              class="font-weight-bold"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'stock' &&
                    editItemProductId == item.productId
                "
              />
              <span
                @dblclick="editProperty(item.productId, 'stock', item.stock)"
                v-else
              >
                {{ item.stock }}
              </span>
            </td>
            <td
              class="font-weight-bold"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'qty' &&
                    editItemProductId == item.productId
                "
              />
              <span
                @dblclick="editProperty(item.productId, 'qty', item.qty)"
                v-else
              >
                {{ item.qty }}
              </span>
            </td>
            <td
              class="font-weight-normal"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'unit' &&
                    editItemProductId == item.productId
                "
              />
              <span
                @dblclick="editProperty(item.productId, 'unit', item.unit)"
                v-else
              >
                {{ item.unit }}
              </span>
            </td>
            <td
              class="font-weight-bold"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'mrp' &&
                    editItemProductId == item.productId
                "
              />
              <span
                @dblclick="editProperty(item.productId, 'mrp', item.mrp)"
                v-else
              >
                {{ item.mrp }}
              </span>
            </td>
            <td
              class="font-weight-bold"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'currentPrice' &&
                    editItemProductId == item.productId
                "
              />
              <span
                @dblclick="
                  editProperty(
                    item.productId,
                    'currentPrice',
                    item.currentPrice
                  )
                "
                v-else
              >
                {{ item.currentPrice }}
              </span>
            </td>
            <td
              class="font-weight-bold"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'discount' &&
                    editItemProductId == item.productId
                "
              />
              <span
                @dblclick="
                  editProperty(item.productId, 'discount', item.discount)
                "
                v-else
              >
                {{ item.discount }}
              </span>
            </td>
            <td
              class="small"
              v-if="user.designation == 'admin' || user.uuid == item.uploadedBy"
            >
              <a
