<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size:28px; cursor:pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-2"></div>
        <div class="col-lg-8 col-md-12 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>SCHEDULE PUSH NOTIFICATIONS
          </h5>
          <div class="row p-3">
            <div class="col-sm-12">
              <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                <i class="fas fa-exclamation-triangle pr-2"></i>Fields marked with ( * ) is important and is to be filled
              </div>

              <form class="form pb-5 mb-4">
                <!-- Title, subtitle -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label class="active">
                      Title
                      <small>(Title of notification)</small>
                    </label>
                    <input
                      type="text"
                      v-model="content.title"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <span class="error text-danger font-weight-bold" v-if="errors[0]">{{errors[0]}}</span>
                  </div>

                  <div class="col-sm-6">
                    <label class="active" for="all category">
                      Subtitle
                      <small>(Subtitle of notification)</small>
                    </label>
                    <input
                      type="text"
                      v-model="content.subtitle"
                      required
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <span class="error text-danger font-weight-bold" v-if="errors[1]">{{errors[1]}}</span>
                  </div>
                </div>

                <!-- body -->
                <div class="form-row">
                  <div class="col">
                    <label>
                      Body *
                      <small>(Body of the notification)</small>
                    </label>
                    <ckeditor
                      :editor="editor"
                      v-model="content.body"
                      :config="editorConfig"
                      class="form-control form-control-sm"
                    ></ckeditor>
                    <span class="error text-danger font-weight-bold" v-if="errors[2]">{{errors[2]}}</span>
                  </div>
                </div>

                <!-- message -->
                <div class="form-row">
                  <div class="col">
                    <label>
                      Message *
                      <small>(Message of the notification)</small>
                    </label>
                    <ckeditor
                      :editor="editor"
                      v-model="content.message"
                      :config="editorConfig"
                      class="form-control form-control-sm"
                    ></ckeditor>
                    <span class="error text-danger font-weight-bold" v-if="errors[3]">{{errors[3]}}</span>
                  </div>
                </div>

                <!-- schedule , interval -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>
                      Schedule*
                      <small>(Do you wanna repeat it?)</small>
                    </label>
                    <select v-model="schedule" class="form-control form-control-sm">
                      <option
                        v-for="timer in schedules"
                        :key="timer.id"
                        :value="timer.id"
                        class="font-weight-bold"
                      >{{timer.name}} <span v-if=timer.at>- {{timer.at}}</span></option>
                    </select>
                    <span class="error text-danger font-weight-bold" v-if="errors[4]">{{errors[4]}}</span>
                  </div>
                  <div class="col-sm-6">
                    <label>
                      Interval*
                      <small>(After how long do you want it to repeat)</small>
                    </label>
                    <select v-model="interval" class="form-control form-control-sm">
                      <option
                        v-for="inter in intervals"
                        :key="inter.id"
                        :value="inter.id"
                        class="font-weight-bold"
                      >{{inter.name}}</option>
                    </select>
                    <span class="error text-danger font-weight-bold" v-if="errors[5]">{{errors[5]}}</span>
                  </div>
                </div>

                <!-- type , send to -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>
                      Type *
                      <small>(To let you define the type of notification)</small>
                    </label>
                    <select v-model="type" class="form-control form-control-sm">
                      <option
                        v-for="typo in types"
                        :key="typo.id"
                        :value="typo.id"
                        class="font-weight-bold"
                      >{{typo.name}}</option>
                    </select>
                    <span class="error text-danger font-weight-bold" v-if="errors[6]">{{errors[6]}}</span>
                  </div>
                  <div class="col-sm-6">
                    <label>
                      Send To*
                      <small>(Receiver of notification)</small>
                    </label>
                    <select v-model="sendTo" class="form-control form-control-sm">
                      <option
                        v-for="receivers in receivers"
                        :key="receivers.id"
                        :value="receivers.id"
                        class="font-weight-bold"
                      >{{receivers.name}}</option>
                    </select>
                    <span class="error text-danger font-weight-bold" v-if="errors[7]">{{errors[7]}}</span>
                  </div>
                </div>

                <!-- Coid,  proid, orderid, actitle -->
                <div class="form-row">
                  <div class="col-sm-6" v-if="type==2">
                    <label class="active">
                      Coid
                      <small>(CategoryId to be displayed in title of notification)</small>
                    </label>
                    <input
                      type="text"
                      v-model="content.coid"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <span class="error text-danger font-weight-bold" v-if="errors[1]">{{errors[1]}}</span>
                  </div>
                  <div class="col-sm-6" v-if="type==1">
                    <label class="active">
                      ProId
                      <small>((ProductId to be item in notification)</small>
                    </label>
                    <input
                      type="text"
                      v-model="content.proid"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <span class="error text-danger font-weight-bold" v-if="errors[1]">{{errors[1]}}</span>
                  </div>
                  <div class="col-sm-6" v-if="type==6">
                    <label class="active">
                      OrderId
                      <small>(OrderId to be order in notification)</small>
                    </label>
                    <input
                      type="text"
                      v-model="content.orderId"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <span class="error text-danger font-weight-bold" v-if="errors[1]">{{errors[1]}}</span>
                  </div>
                  <div class="col-sm-6">
                    <label class="active">
                      Actitle
                      <small>(Activity title to take you somewhere)</small>
                    </label>
                    <input
                      type="text"
                      v-model="content.actitle"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <!-- <span class="error text-danger font-weight-bold" v-if="errors[1]">{{errors[1]}}</span> -->
                  </div>
                </div>

                <!-- start , expiry date -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>
                      Start Date*
                      <small>(When this notification should start)</small>
                    </label>
                    <input type="date" v-model="startDate" class="form-control form-control-sm" />
                    <span class="error text-danger font-weight-bold" v-if="errors[8]">{{errors[8]}}</span>
                  </div>
                  <div class="col-sm-6">
                    <label>
                      Expiry Date*
                      <small>(When this notification should expire)</small>
                    </label>
                    <input type="date" v-model="expiryDate" class="form-control form-control-sm" />
                    <span class="error text-danger font-weight-bold" v-if="errors[9]">{{errors[9]}}</span>
                  </div>
                </div>

                <!-- image -->
                <div class="form-row">
                  <!-- image -->
                  <div class="col-sm-6 text-center">
                    <div class="switch">
                      <label>
                        Add Path *
                        <input
                          type="checkbox"
                          v-model="imageOption"
                          @click="media=null"
                          :disables="disable"
                        />
                        <span class="lever"></span>
                        Upload
                      </label>
                    </div>

                    <div class="col-12" v-if="imageOption==false">
                      <label>Image Path</label>
                      <input
                        type="text"
                        v-model="image"
                        :disabled="disable"
                        class="form-control form-control-sm"
                      />
                    </div>

                    <div class="col-12 text-center" v-else>
                      <label class="btn btn-sm btn-info text-center" style="color:#fff">
                        <i class="fas fa-image pr-2"></i>
                        <div class="d-inline font-weight-bold">ADD IMAGE</div>
                        <input type="file" class="file" @change="change('image')" ref="image" />
                      </label>
                      <div class="d-inline-block" v-if="media">
                        <a
                          class="float-right"
                          style="color:#424242;"
                          href="#"
                          title="delete this brand"
                          @click.prevent="removeImage('image',0)"
                        >
                          <i class="fa fa-sm fa-times"></i>
                        </a>
                        <img :src="media" class="img-fluid" />
                      </div>
                      <div class="d-inline-block" v-else-if="image">
                        <a
                          class="float-right"
                          style="color:#424242;"
                          href="#"
                          title="delete this brand"
                          @click.prevent="removeImage('image','image')"
                        >
                          <i class="fa fa-sm fa-times"></i>
                        </a>
                        <img :src="image" class="img-fluid" v-if="image" />
                      </div>
                    </div>
                  </div>
                </div>

                <!-- buttons -->
                <div class="form-row">
                  <div class="col text-center">
                    <button
                      class="btn btn-sm btn-go-green font-weight-bold d-inline"
                      type="submit"
                      @click.prevent="save"
                      :disabled="disable"
                    >
                      <i class="fas fa-save pr-1"></i>
                      SAVE
                    </button>
                    <router-link to="#" class="btn btn-sm btn-secondary">
                      <i class="fas fa-ban pr-1"></i>
                      <div class="font-weight-bold d-inline">CANCEL</div>
                    </router-link>
                  </div>
                </div>

                <div class="form-row" v-if="errors[10]">
                  <div class="col text-center text-danger">{{errors[10]}}</div>
                </div>
              </form>
            </div>
          </div>
        </div>
        <div class="col-lg-2"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>


<script>
import axios from "axios";
import $ from "jquery";
import Vue from "vue";
import firebase from 'firebase/app'
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import store from "@/store/store";
import Loader from "@/components/Loader";

export default {
  name: "SendPushNotification",
  components: { Loader },

  data() {
    return {
      schedule: 0,
      interval: 0,
      image: null,
      sendTo: 0,
      type: 0,
      content: {
        title: null,
        subtitle: null,
        message: null,
        body: null,
        actitle: null,
        orderid: null,
        proid: null,
        serialnum: 0,
        coid: 0
      },
      startDate: null,
      expiryDate: null,

      media: null,
      imageOption: true,
      errors: [],
      disable: false,
      success: false,
      message: null,
      types: [],
      receivers: [],
      schedules: [],
      intervals: [],

      editor: ClassicEditor,
      editorData: [],
      editorConfig: {},
    };
  },

  beforeRouteLeave(to, from, next) {
    if(confirm("Gaando thai gyo chu")){
      next();
    } else {
      next(false);
    }
  },

  
  computed: {
    user() {
      return store.state.user;
    }
  },

  created() {
    this.getNotificationTypes();
    this.getNotificationSchedule();
    this.getNotificationInterval();
    this.getNotificationReciever();
  },

  methods: {
    change(type) {
      let files = this.$refs.thumbnail.files;
      this.createImage(files[0]);
    },

    createImage(file) {
      let reader = new FileReader();
      reader.onload = e => {
        this.media = e.target.result;
      };
      this.image = null;
      reader.readAsDataURL(file);
    },

    goBack() {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push("/inventory");
    },

    getCategories() {
      let url = domain + "cat";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            const cat = {
              name: doc.name,
              subcatid: doc.subcatcode
            };
            this.categories.push(cat);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    getCoCategories() {
      this.ModalContentLoading = true;
      this.cocategories = [];
      let url = domain + "cocat";
      if (this.subcategories[this.subcategory].active == "0") {
        this.active = false;
      }
      axios
        .get(url, {
          params: {
            coid: this.subcategories[this.subcategory].coid
          }
        })
        .then(res => {
          if (res.data) {
            res.data.forEach(doc => {
              const cocat = {
                name: doc.name,
                itemId: doc.item_id,
                priceBandValue: doc.priceBandValue,
                serialNum: doc.serialNum
              };
              this.cocategories.push(cocat);
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },

    getNotificationTypes() {
      let url = domain + "user/notification/push/type";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            const data = {
              id: doc.id,
              name: doc.name
            };
            this.types.push(data);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    getNotificationSchedule() {
      let url = domain + "user/notification/push/schedule";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            const data = {
              id: doc.id,
              name: doc.name,
              at: doc.at  
            };
            this.schedules.push(data);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    getNotificationInterval() {
      let url = domain + "user/notification/push/interval";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            const data = {
              id: doc.id,
              name: doc.name
            };
            this.intervals.push(data);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    getNotificationReciever() {
      let url = domain + "user/notification/push/receiver";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            const data = {
              id: doc.id,
              name: doc.name
            };
            this.receivers.push(data);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    getSubCategories() {
      this.subcategories = [];
      this.cocategories = [];
      let url = domain + "subcat";
      if (this.categories[this.category].active == "0") {
        this.active = false;
      }
      axios
        .get(url, {
          params: {
            subcatid: this.categories[this.category].subcatid
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            const subcat = {
              name: doc.name,
              coid: doc.coid,
              active: doc.active
            };
            this.subcategories.push(subcat);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    save() {
      if (this.user && this.validateData()) {
        let url = domain + "user/notification/push";
        axios
          .post(
            url,
            {
              content: this.content,
              image: this.image,
              sendTo: this.sendTo,
              schedule: this.schedule,
              interval: this.interval,
              type: this.type,
              startDate: this.startDate,
              expiryDate: this.expiryDate
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            //// to show
            this.message = res;
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          })
          .catch(err => {
            //// to show
            this.message = "Data Upload Failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    },

    validateData() {
      this.errors = [];
      if (
        (this.content.title &&
          this.content.subtitle &&
          this.content.message &&
          this.content.body &&
          this.type > 0 &&
          this.schedule > 0 &&
          this.interval > 0 &&
          this.startDate &&
          (this.expiryDate == null || this.expiryDate >= now()),
        this.sendTo > 0)
      ) {
        return true;
      } else {
        if (!this.title) {
          Vue.set(this.errors, 0, "Add Title");
        }
        if (!this.subtitle) {
          Vue.set(this.errors, 1, "Add Subtitle");
        }
        if (!this.body) {
          Vue.set(this.errors, 2, "Add Body");
        }
        if (!this.message) {
          Vue.set(this.errors, 3, "Add Message");
        }
        if (this.schedule <= 0) {
          Vue.set(this.errors, 4, "Choose valid schedule");
        }
        if (this.interval <= 0) {
          Vue.set(this.errors, 5, "Choose valid interval");
        }
        if (this.type <= 0) {
          Vue.set(this.errors, 6, "Choose valid type");
        }
        if (this.sendTo <= 0) {
          Vue.set(this.errors, 7, "Choose valid receivers");
        }

        Vue.set(
          this.errors,
          10,
          "Something is missing!! Check out what you left"
        );
        return false;
      }
    }
  }
};
</script>
<style scoped>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}
</style>



