<template>
  <div>
    <div class="row card-deck dashboard-cards p-4">
      <!-- total items -->
      <div class="col-lg-3 card content border-1 shadow-sm">
        <div class="card-body text-center">
          <div class="media">
            <div class="shopping-cart">
              <img src="../../assets/shopping-cart.svg" class="img-fluid align-self-center" />
            </div>
            <div class="media-body card-content">
              <span>Total Items</span>
              <h4>{{totalItems}}</h4>
            </div>
          </div>
        </div>
      </div>
      <!-- actuve items -->
      <div class="col-lg-3 card content border-1 shadow-sm">
        <div class="card-body text-center">
          <div class="media">
            <div class="revenue">
              <img src="../../assets/ruppee.svg" class="img-fluid align-self-center" />
            </div>
            <div class="media-body card-content">
              <span>Active Items</span>
              <h4>{{activeItems}}</h4>
            </div>
          </div>
        </div>
      </div>
      <!-- inactive items -->
      <div class="col-lg-3 card content border-1 shadow-sm">
        <div class="card-body text-center">
          <div class="media">
            <div class="user">
              <img src="../../assets/user.svg" class="img-fluid align-self-center" />
            </div>
            <div class="media-body card-content">
              <span>Inactive Items</span>
              <h4>{{totalItems - activeItems}}</h4>
            </div>
          </div>
        </div>
      </div>

      <div class="col-lg-3 card content border-1 shadow-sm">
        <div class="card-body text-center">
          <div class="media">
            <div class="income">
              <img src="../../assets/coins.svg" class="img-fluid align-self-center" />
            </div>
            <div class="media-body card-content">
              <span>Less Stock</span>
              <h4>{{lessStock}}</h4>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="row p-4">
      <div class="col-lg-6">
        <div class="card content border-1 shadow-sm">
          <div class="card-body">
            <div class="media pb-2">
              <img
                src="../../assets/exclamation-mark.svg"
                class="img-fluid align-self-center exclamation-mark mr-2"
              />
              <div class="media body">
                <h5 class="font-weight-medium pt-2">Low Stock Items</h5>
              </div>
            </div>
            <div class="table-responsive">
              <table class="table table-hover table-sm table-borderless table-content">
                <thead class="small">
                  <tr>
                    <th class="text-center">S.No</th>
                    <th class="text-center">Stock</th>
                    <th class="text-center">No. of Items</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(stock,index) in lowerStockData" :key="index+1">
                    <td class="text-center">{{index+1}}</td>
                    <td class="text-center">{{stock.stock}}</td>
                    <td class="text-center">{{stock.count}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      <div class="col-lg-6">
        <div class="card content border-1 shadow-sm">
          <div class="card-body">
            <Charts :options="uploadsChartOptionsBar"></Charts>            
          </div>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-md-6">
        <div class="p-4">
          <Charts :options="uploadsChartOptionsBar"></Charts>
        </div>
      </div>
      <div class="col-md-6">
        
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
import Vue from "vue";
import { domain } from "@/store/variable";
import store from "@/store/store";
import Loader from "../Loader";
import Charts from "vue-echarts";

export default {
  name: "InventoryDashBoard",

  components: {
    Charts,
    Loader
  },

  computed: {
    user() {
      return store.state.user;
    }
  },

  data() {
    return {
      activeItems: 0,
      totalItems: 0,
      lessStock: 0,
      lowerStockData: [],
      uploadsChartOptionsBar: { 
        xAxis: {
          data: ["7", "6", "5", "4", "3", "2", "1"],
          name: "Last 7 days"
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            type: "line",
            data: [0,0,0,0,0,0,0]
          }
        ]
      },
      chartOptionsBar: {
        xAxis: {
          data: ["7", "6", "5", "4", "3", "2", "1"],
          name: "Last 7 days",
          nameLocation: 'middle'
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            type: "bar",
            data: [1,3,4,0,0,0,0]
          }
        ]
      }
    };
  },

  mounted() {
    this.getCardData();
    this.getLessStockData();
    this.getWeekUploads();
  },

  methods: {
    getCardData() {
      let url = domain + "item";

      //total items
      axios
        .get(url, {
          params: {
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            this.totalItems = res.data;
          }
        });
      //active ites
      axios
        .get(url, {
          params: {
            active: 1,
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            this.activeItems = res.data;
          }
        });

      //less ites
      axios
        .get(url, {
          params: {
            compare: ["stock", "<", 4],
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            this.lessStock = res.data;
          }
        });
    },

    getLessStockData() {
      let url = domain + "item";

      //with 0 stock
      axios
        .get(url, {
          params: {
            stock: 0,
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            this.lowerStockData.push({
              stock: 0,
              count: res.data
            });
          }
        });

      //1 items
      axios
        .get(url, {
          params: {
            stock: 1,
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            this.lowerStockData.push({
              stock: 1,
              count: res.data
            });
          }
        });

      //2 items
      axios
        .get(url, {
          params: {
            stock: 2,
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            this.lowerStockData.push({
              stock: 2,
              count: res.data
            });
          }
        });

      //3 items
      axios
        .get(url, {
          params: {
            stock: 3,
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            this.lowerStockData.push({
              stock: 3,
              count: res.data
            });
          }
        });
    },

    getWeekUploads(){
      let url = domain + "item";

      //same day
      axios
        .get(url, {
          params: {
            compare: ['uploadedAt','=',0],
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            Vue.set(this.uploadsChartOptionsBar.series[0].data,0,res.data);
          }
        });

      //with 1 stock
      axios
        .get(url, {
          params: {
            compare: ['uploadedAt','=',1],
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            Vue.set(this.uploadsChartOptionsBar.series[0].data,1,res.data);
          }
        });


      //with 1 stock
      axios
        .get(url, {
          params: {
             compare: ['uploadedAt','=',2],
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            Vue.set(this.uploadsChartOptionsBar.series[0].data,2,res.data);
          }
        });


      axios
        .get(url, {
          params: {
             compare: ['uploadedAt','=',3],
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            Vue.set(this.uploadsChartOptionsBar.series[0].data,3,res.data);
          }
        });

      axios
        .get(url, {
          params: {
             compare: ['uploadedAt','=',4],
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            Vue.set(this.uploadsChartOptionsBar.series[0].data,4,res.data);
          }
        });

      axios
        .get(url, {
          params: {
             compare: ['uploadedAt','=',5],
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            Vue.set(this.uploadsChartOptionsBar.series[0].data,5,res.data);
          }
        });

      //with 1 stock
      axios
        .get(url, {
          params: {
             compare: ['uploadedAt','=',6],
            count: "laaiye"
          }
        })
        .then(res => {
          if (res.data) {
            Vue.set(this.uploadsChartOptionsBar.series[0].data,6,res.data);
          }
        });

    }
  }
};
</script>

<style src="@/assets/css/dashBoards.css" scoped>
</style>



